// since our elements all add a bit of spacing, the card needs less to give the
// first/last element room

$card-padding-vertical-real: $card-padding-vertical - $spacing-vertical-half;
$card-color-separator: $color-separator;
$card-color-background: $color-white;
$card-shadow: 0 rem(2) rem(8) rgba($color-blue-400, 0.08);
$card-border-radius: rem(8);

$card-focus-background: rgba($color-gray-100, 0.8);

$functioncard-spacing: rem(15);
$functioncard-spacing-prominent: $functioncard-spacing + rem(5);
$functioncard-padding: rem(25);

$functioncard-horizontal-indicator-width-large: 23%;
$functioncard-horizontal-indicator-width-regular: 15%;
$functioncard-horizontal-button-width: 25%;

$productcard-padding: rem(16);
$productcard-padding-reduced: rem(8);

$productcard-horizontal-button-width: 30%;

$productcard-icon-size-default: rem(64);
$productcard-icon-size-large: rem(96);
