// this file handles overview cards - the tiled product-summary-cards on the dashboard
$g-card-overview-height: rem(280);
$g-card-overview-width-ideal: rem(255);

$g-card-overview-header-footer-height: rem(46);
$g-card-overview-list-footer-width: rem(72);
$g-card-overview-list-footer-width-lg: $g-card-overview-list-footer-width + rem(220);

$g-card-overview-dual-currency-offset: rem(112);

@mixin inner-focusable {
    transition-property: border-color;
    border-width: rem(2);

    @include increase-specificity(1) {
        &:focus {
            border-color: var(--g-color-border-focus);
            box-shadow: none;
        }
    }
}

.g-card-overview {
    @include increase-specificity(2) {
        display: flex;
    }

    position: relative;
    padding: 0;
    transition: $input-transition-property $input-transition-duration;

    &:not(.g-card-overview-widget):hover {
        box-shadow: 0 rem(4) rem(12) rgba(var(--rgb-blue-400), 0.16);
    }

    .g-card-overview-title {
        @include increase-specificity(1) {
            padding: 0;
        }

        overflow: hidden;
        color: var(--g-color-text-primary);
        font-size: rem(16);
        font-weight: $typo-x-font-weight-semi-bold;
        text-overflow: ellipsis;
    }

    .g-card-overview-action-default {
        @include line-clamp(2);

        &:hover {
            text-decoration: none;
        }

        &:focus {
            box-shadow: none;
        }

        &::before {
            @include cover-parent($z-index: z-index-level(base, 1));

            content: '';
        }

        &.g-button {
            display: inline;
            position: static;
            width: auto;
            min-height: 0;
            line-height: rem(20);

            &:focus {
                @include increase-specificity(1) {
                    box-shadow: none;
                }
            }

            .g-button-label {
                white-space: normal;
            }
        }
    }

    @include increase-specificity(1) {
        .g-micro {
            font-size: rem(14);
        }
    }

    .g-card-overview-info {
        display: flex;

        .g-card-overview-info-data {
            display: grid;
            flex-grow: 1;
            flex-shrink: 1;
            width: 100%;
            min-width: 0;
            margin: rem(-4) 0 rem(-2);
            line-height: rem(20);

            .g-card-overview-info-alias,
            .g-micro {
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
            }

            .g-card-overview-info-alias {
                margin: rem(2) 0;

                .g-card-overview-title {
                    line-height: rem(20);
                }
            }
        }

        .g-card-overview-stripe {
            right: auto;
            bottom: 0;
            left: 0;
            width: $colored-stripe-size;
            height: auto;
        }
    }

    .g-amount {
        font-size: rem(20);
    }

    @include increase-specificity(1) {
        .g-amount-converted {
            @extend %amount-no-superscript;

            padding: 0;
            color: var(--g-color-text-primary);
            font-size: rem(12);
            font-weight: $typo-x-font-weight;
            line-height: rem(16);
        }
    }

    .g-card-overview-figure {
        flex-shrink: 0;
        order: -1;
        color: var(--g-color-text-primary);
    }

    .g-major {
        font-weight: $typo-x-font-weight-semi-bold;
    }

    .g-button,
    .g-tooltip-trigger {
        @include cover-parent-exclude(z-index-level(base, 3));
    }

    .g-card-overview-extra-amount {
        @extend %amount-no-superscript;

        padding: 0;
        color: var(--g-color-text-secondary);
        font-size: rem(14);
        font-weight: $typo-x-font-weight-regular;
        line-height: rem(21);
    }

    @include element-focus('-within');

    .g-card-overview-footer {
        z-index: z-index-level(base, 3);
        flex-grow: 0;
        flex-shrink: 0;

        &-right-aligned {
            justify-content: right;
        }

        > .g-button {
            margin-right: rem(12);
            padding-right: rem(8);
            padding-left: rem(8);

            &:focus {
                border-color: var(--g-color-border-focus);
                box-shadow: none;
            }
        }
    }

    &.g-inverted {
        .g-card-overview-title,
        .g-card-overview-action-default:active,
        .g-faded,
        .g-card-overview-extra-amount {
            color: inherit;
        }

        .g-card-overview-footer {
            border-top-color: rgba(var(--rgb-white), 0.15);
        }
    }
}

.g-card-overview-listview {
    flex-flow: row;
    min-height: rem(72);
    @include media-breakpoint-only(xs) {
        align-items: flex-start;
    }
    @include media-breakpoint-up(sm) {
        align-items: center;
    }

    .g-card-overview-title {
        right: $g-card-overview-list-footer-width;

        a {
            left: rem(80);
        }
    }

    .g-card-overview-info-container {
        min-width: 0;
        padding-right: 0;

        .g-card-overview-info {
            align-items: center;
            padding-left: rem(5);

            .g-card-overview-info-data {
                align-items: center;
                margin-top: 0;
                margin-bottom: 0;

                .g-card-overview-info-alias-area {
                    display: flex;
                    align-items: baseline;
                    overflow: hidden;

                    * {
                        margin-right: rem(8);
                    }

                    @include media-breakpoint-only(xs) {
                        .g-card-overview-action-default {
                            @include line-clamp(1);
                        }
                    }

                    .badge // DEPRECATED
                    .g-badge {
                        vertical-align: rem(-3);
                    }

                    .g-micro {
                        line-height: rem(24);
                    }
                }
            }

            &.g-card-overview-info-totaled {
                .g-card-overview-info-amount {
                    display: flex;
                    flex-direction: column;

                    .g-card-overview-info-amount-dual {
                        display: flex;
                        flex-direction: column;

                        .g-card-overview-info-amount-inner {
                            overflow: hidden;
                        }

                        > :nth-child(2) {
                            width: $g-card-overview-dual-currency-offset;
                            text-align: left;

                            @include media-breakpoint-up(sm) {
                                align-self: center;
                                padding-left: rem(6);
                            }
                        }

                        @include media-breakpoint-up(sm) {
                            flex-direction: row;
                            justify-content: flex-end;

                            .g-card-overview-info-amount-offseted-for-dual {
                                // a single amount in a dual currency view
                                padding-right: $g-card-overview-dual-currency-offset;
                            }
                        }
                    }
                }

                @include media-breakpoint-up(sm) {
                    .g-card-overview-info-data {
                        grid-template-columns: auto auto;
                        grid-template-rows: repeat(6, max-content);
                    }

                    .g-card-overview-info-amount {
                        grid-column: 2 / 3;
                        grid-row: 1 / 99;
                        min-width: 0;
                        margin-left: rem(10);
                        text-align: right;

                        .g-amount {
                            padding-top: rem(2);
                        }

                        p {
                            padding: 0;
                            @include ellipsis;
                        }

                        p + .g-card-overview-extra-amount {
                            margin-top: rem(-4);
                        }
                    }
                }
            }

            &.g-card-overview-info-verbose {
                @include media-breakpoint-up(sm) {
                    .g-card-overview-badge-area {
                        padding-top: rem(4);
                    }
                }
            }
        }

        .g-card-overview-figure {
            height: auto;
            margin-right: rem(16);
        }

        @include media-breakpoint-only(xs) {
            .g-card-overview-figure {
                display: none;
            }

            .g-micro {
                display: none;
            }
        }
    }

    &:not(.g-card-overview-promotion) {
        .g-card-overview-body {
            display: none;
        }
    }

    .g-card-overview-footer {
        display: flex;
        align-items: center;
        width: $g-card-overview-list-footer-width;
        margin-top: auto;
        margin-bottom: auto;
        padding: 0;

        &.g-card-overview-footer-full-width {
            width: $g-card-overview-list-footer-width-lg;
        }

        @include media-breakpoint-down(md) {
            justify-content: center;

            &.g-card-overview-footer-full-width {
                width: $g-card-overview-list-footer-width;
            }

            .g-card-overview-footer-content {
                display: none;
            }
        }

        > .g-button {
            @include inner-focusable;
        }

        .g-card-overview-footer-content {
            padding: 0 rem(16) 0 rem(30);

            .g-btn-overview-footer {
                width: rem(190);

                &:focus {
                    border-color: var(--g-color-border-focus);
                    box-shadow: none;
                }
            }
        }
    }
}

.g-card-overview-gridview {
    align-items: stretch;
    min-width: rem(210);
    height: $g-card-overview-height;

    .g-card-overview-title {
        bottom: $g-card-overview-header-footer-height;
    }

    .g-card-overview-info-container {
        flex-grow: 0;
        flex-shrink: 0;

        .g-card-overview-info {
            flex-direction: row-reverse;
            padding-top: rem(10);

            .g-micro,
            .g-card-overview-badge-area {
                margin-top: rem(-4);
            }

            .g-card-overview-figure {
                margin-left: rem(4);
            }

            .g-card-overview-stripe {
                width: 100%;
                height: rem(4);
            }
        }

        .g-card-overview-grid-amounts {
            display: flex;
            flex-direction: column;

            p {
                padding: 0;
            }

            .g-meter {
                margin: rem(7) 0;
            }

            .g-amount {
                line-height: 1.5;
            }

            .g-card-overview-extra-amount {
                line-height: rem(20);
            }

            .g-card-overview-extra-amount-offsetted {
                margin-top: rem(30);
            }
        }

        .meter {
            margin: rem(7) 0;
        }
    }

    .g-card-overview-footer {
        display: flex;
        align-items: center;
        width: 100%;
        height: $g-card-overview-header-footer-height;
        padding: 0;
        border-top: rem(1) var(--g-color-border-secondary) solid;

        .g-card-overview-footer-content {
            flex: 1;
            padding-right: rem(8);
            overflow: hidden;
        }

        .g-button {
            @include inner-focusable;

            width: auto;
            min-height: rem(32);
            margin-left: rem(8);
            padding-right: rem(8);
            padding-left: rem(8);
        }
    }
}

.g-card-group-vertical {
    .g-card-overview-listview {
        &:focus-within,
        &:hover {
            background-color: var(--g-color-surface-hover);
            box-shadow: none;
        }
    }
}

.g-card-overview-body {
    flex-grow: 1;
    overflow-x: hidden;
    overflow-y: hidden;

    &.g-card-overview-body-interactive {
        position: relative;
        z-index: z-index-level(base, 2);
    }

    &.g-card-overview-body-unpadded {
        padding: 0;
    }
}

// --- WIDGET CARD --------------------------------------------------------------

.g-card-overview-widget {
    height: rem(320);

    .g-card-overview-header {
        min-height: $g-card-overview-header-footer-height;
        border-bottom: rem(1) var(--g-color-border-secondary) solid;
        white-space: nowrap;
    }

    .g-card-overview-body {
        height: 100%;
        overflow-y: visible;
    }
}

// --- PROMO CARD --------------------------------------------------------------

.g-card-overview-promotion {
    .g-card-overview-body {
        flex-grow: 0;
        overflow: hidden;

        p {
            text-align: left;
        }
    }

    &.g-card-overview-listview {
        > .g-button {
            margin-right: rem(8);
            padding: rem(8);
        }

        .g-card-overview-body {
            display: flex;
            flex: 1;
            padding-left: calc(var(--card-padding-horizontal) + $colored-stripe-size);
            text-align: left;

            .g-card-overview-icon {
                width: rem(48);
                margin-right: rem(8);
            }

            .g-card-overview-promotion-info {
                padding-left: rem(8);

                .g-card-overview-action-default {
                    h2,
                    h3,
                    h4,
                    h5,
                    h6 {
                        @include line-clamp(1);

                        padding: 0;
                    }
                }

                p {
                    padding: 0;
                    @include line-clamp(2);
                    @include media-breakpoint-up(md) {
                        @include line-clamp(1);
                    }
                }
            }
        }
    }

    &.g-card-overview-gridview {
        justify-content: flex-end;
        background-position: center;
        background-size: cover;

        > .g-button {
            @include inner-focusable;

            position: absolute;
            z-index: z-index-level(base, 3);
            top: rem(8);
            right: rem(8);
            margin: 0;
        }

        .g-card-overview-body {
            overflow: hidden;
            text-align: center;
            text-overflow: ellipses;

            .g-micro {
                @include line-clamp(4);
            }

            .g-card-overview-icon {
                width: rem(96);
            }
        }
    }
}

// --- CARD GROUP --------------------------------------------------------------

.g-card-overview-group {
    display: flex;
    flex-direction: column;
    gap: rem(15);
    margin: rem(20) 0;

    .g-card-overview {
        margin: 0;
    }
}

.g-card-overview-group-layout-grid {
    @include media-breakpoint-only(xs) {
        $g-card-overview-group-enlargement: rem(15);
        $g-card-overview-group-gap-mobile: rem(15);

        flex-direction: row;
        gap: $g-card-overview-group-gap-mobile;
        margin: rem(20) #{-$g-card-overview-group-enlargement} rem(10);
        padding: 0 $g-card-overview-group-enlargement rem(10);
        overflow: auto hidden;
        scroll-behavior: smooth;
        scroll-snap-type: x mandatory;

        .g-card-overview {
            flex: 1 0 $g-card-overview-width-ideal;
            scroll-snap-align: start;
            scroll-margin-left: $g-card-overview-group-enlargement;
        }

        &::after {
            content: '';
            flex: 0 0
                calc(100% - #{$g-card-overview-width-ideal} - #{$g-card-overview-group-gap-mobile});
        }

        &:has(.g-card-overview:only-child) {
            &::after {
                content: none;
            }
        }
    }
    @include media-breakpoint-up(sm) {
        --columns: 2;

        display: grid;
        grid-template-columns: repeat(var(--columns), 1fr);
        gap: rem(30);
    }
    @include media-breakpoint-up(md) {
        --columns: 3;
    }
    @include media-breakpoint-up(lg) {
        --columns: 4;
    }
}

.g-card-overview-group-layout-widget {
    --columns: 1;

    display: grid;
    grid-template-columns: repeat(var(--columns), 1fr);
    gap: rem(30);

    @include media-breakpoint-up(lg) {
        --columns: 2;
    }
}
