// --- fancy dropdowns ---------------------------------------------------------

.g-dropdown {
    width: 100%;

    &.dropdown {
        p,
        ul,
        ol {
            // bootstrap fixes
            margin: 0;
        }
    }

    .dropdown-menu {
        // bootstrap styles override
        position: inherit;
        z-index: z-index-level(dropdown);
        width: 100%;
        min-width: rem(160);
        max-height: rem(300);
        margin: 0;
        padding: 0;
        float: none;
        overflow-x: hidden;
        overflow-y: auto;
        border: 0 none;
        color: var(--g-color-text-primary);

        &:focus {
            outline: 0;
        }
    }

    %dropdown-item,
    .dropdown-item {
        @include btn;
        @include typo(p, $lineHeight: false);

        display: flex;
        position: relative;
        align-items: flex-start;
        justify-content: flex-start;
        height: auto;
        padding: (rem(15) - $typo-p-offset-top) rem(10);
        border-radius: 0;
        color: var(--g-color-text-primary);
        font-weight: $typo-x-font-weight-regular;
        line-height: rem(22);
        text-align: left;
        white-space: normal;

        &:hover,
        &:focus,
        &:active,
        &.highlight {
            outline: none;
            background-color: var(--g-color-surface-hover);
            box-shadow: none;
            text-decoration: none;
        }

        &.selected {
            color: var(--g-color-text-primary);
            font-weight: $typo-x-font-weight-semi-bold;
        }

        &:disabled,
        &.disabled {
            background-color: inherit;
            color: var(--g-color-text-disabled);
        }

        &.border {
            border: 0 none !important; // stylelint-disable-line declaration-no-important
            border-bottom: rem(1) solid var(--g-color-border-secondary) !important; // stylelint-disable-line declaration-no-important
        }

        > img {
            align-self: center;
            margin-right: rem(8);
        }
    }

    .dropdown-menu-right {
        // when used in an input group, we do not break content but make the dropdown
        // as wide as necessary starting from the right
        width: auto;
        min-width: 100%;

        .dropdown-item {
            padding-right: rem(20);
            white-space: nowrap;
        }
    }

    .custom-control {
        margin-top: rem(-7);
        margin-right: rem(10);
        margin-bottom: rem(-7);
        padding-left: $custom-control-indicator-size;
    }

    &:not(.g-dropdown-product) {
        .dropdown-item {
            @include media-breakpoint-up(sm) {
                display: flex;
                align-items: center;
                justify-content: space-between;

                .g-amount {
                    margin-left: rem(15);
                    text-align: right;
                }
            }

            .g-amount {
                display: block;
            }
        }
    }

    &.disabled,
    &:disabled {
        cursor: not-allowed;

        .dropdown-toggle {
            &,
            &:focus {
                &,
                &:hover {
                    border-color: var(--g-color-border-disabled);
                    opacity: 1;
                    background-color: var(--g-color-surface-disabled);
                    box-shadow: none;
                    color: var(--g-color-text-disabled);

                    &::after {
                        color: var(--g-color-text-disabled);
                        cursor: not-allowed;
                    }

                    .g-faded {
                        color: var(--g-color-text-disabled);
                    }
                }
            }
        }
    }

    .g-faded {
        color: var(--g-color-text-secondary);
    }
}

%dropdown-toggle {
    @extend %select;
    @extend %select-arrow;

    width: 100%;
    min-height: $input-control-size;
    padding: $input-control-padding rem(26) $input-control-padding rem(10) !important; // stylelint-disable-line declaration-no-important

    &.show,
    &[aria-expanded='true'] {
        @extend %select-focus;

        &::after {
            transform: rotate(180deg);
        }
    }
}

.dropdown-toggle {
    @extend %dropdown-toggle;
}

.g-dropdown-separator {
    .dropdown-item {
        &:not(:last-child) {
            border-bottom: rem(1) solid var(--g-color-border-secondary);
        }
    }
}

.g-dropdown-product {
    .dropdown-item {
        display: block;

        &.selected {
            font-weight: $typo-x-font-weight-regular;
        }
    }

    &:not(.g-dropdown-product-condensed) {
        .dropdown-item {
            position: relative;
        }

        .g-product-info,
        .g-contact-info {
            min-height: rem(56);
            padding-left: $colored-stripe-size;
        }

        .g-product-info-stripe {
            border-radius: 0 !important; // stylelint-disable-line declaration-no-important
        }
    }

    .dropdown-toggle,
    &.dropdown-toggle {
        .g-faded,
        .g-iban {
            @include typo(p-micro, $lineHeight: false);

            line-height: 1;
        }

        > * {
            width: auto;
        }
    }

    &-select-all-content {
        padding-left: rem(5);
    }

    &-checkbox {
        @include increase-specificity(2) {
            margin: auto rem(9) auto rem(10);
        }
    }
}

.input-group {
    .g-dropdown {
        &:not(:first-child) {
            .form-control,
            .dropdown-toggle {
                border-top-left-radius: 0;
                border-bottom-left-radius: 0;
            }
        }

        &:not(:last-child) {
            .form-control,
            .dropdown-toggle {
                border-top-right-radius: 0;
                border-bottom-right-radius: 0;
            }
        }

        &.input-group-prepend,
        &.input-group-append {
            width: auto;

            .dropdown-item {
                @extend %dropdown-item;
            }
        }
    }

    > .input-group-prepend,
    > .input-group-append {
        > .dropdown-toggle {
            @include increase-specificity(2) {
                @extend %dropdown-toggle;
            }
        }
    }

    > .dropdown-toggle {
        &:not(:first-child) {
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
        }

        &:not(:last-child) {
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
        }
    }
}
