$nav-side-border-radius: rem(4);

.g-sidenav {
    margin-top: $spacing-vertical-half - rem(1);

    li a,
    .g-sidenav-heading {
        display: flex;
        position: relative;
        align-items: center;
        padding: rem(8) rem(10);
        transition: $input-transition-property $input-transition-duration;
        border-radius: $nav-side-border-radius;
        color: var(--g-color-text-primary);
        font-weight: $typo-x-font-weight-regular;
        text-decoration: none;

        &:focus {
            z-index: z-index-level(base, 1);
        }

        &:hover {
            background-color: var(--g-color-surface-primary);
        }

        // first span is the text label
        span:nth-of-type(1) {
            @include ellipsis;

            margin-right: auto;

            .g-sidenav-external-icon {
                margin-left: rem(8);
            }
        }

        .g-sidenav-icon {
            margin-right: rem(8);
        }

        .badge, // DEPRECATED
        .g-badge,
        .g-pill {
            margin-left: rem(8);
        }

        .g-button {
            padding-right: 0;
        }
    }

    .g-sidenav-heading {
        button {
            margin-top: 0;
            margin-left: 0;
        }
    }

    ul,
    li {
        margin: 0;
        padding: 0;
    }

    ul {
        list-style-type: none;
    }

    li {
        margin-top: rem(1);

        &::before {
            content: none;
            display: none;
        }

        &.selected {
            border-radius: $nav-side-border-radius;
            background-color: var(--g-color-surface-primary);

            a:not(:only-child) {
                @include border-bottom-radius(0);
            }

            &:not(.selected-within) {
                > a {
                    font-weight: $typo-x-font-weight-semi-bold;
                }
            }

            ul {
                display: block;

                a {
                    &:focus,
                    &:hover {
                        @include border-top-radius(0);
                    }
                }

                li:not(:last-child) a {
                    &:focus,
                    &:hover {
                        @include border-bottom-radius(0);
                    }
                }
            }
        }
    }

    .g-sidenav-offseted-label {
        margin-left: rem(32);
    }
}

.g-navbar-secondary {
    max-height: rem(61);
    padding: 0;
    overflow: hidden;
    background-color: var(--g-color-interactive-primary);
    color: var(--g-color-text-inverted-primary);

    ul,
    li {
        margin: 0;
        padding: 0;
    }

    li::before {
        content: none;
    }

    li a.selected {
        text-decoration: underline;
    }

    .container {
        .nav-link {
            margin-top: rem(8);
            margin-bottom: rem(8);
            color: var(--g-color-text-inverted-primary);
            font-weight: $typo-x-font-weight-regular;

            &:hover {
                text-decoration: underline;
            }
        }

        .g-nav-main {
            a {
                margin-right: rem(20);
            }
        }

        .g-nav-tools {
            svg {
                width: rem(24);
                height: rem(24);
                font-size: rem(24);
                line-height: rem(24);
            }
        }
    }

    // interim hacks
    .dropdown-menu {
        a {
            color: var(--g-color-text-primary);
        }
    }
}
