// ─────────────────────────────────────────────────────────────────────────────
// mixins
// ─────────────────────────────────────────────────────────────────────────────

@mixin button-hover($bg, $border-color: null, $shadow-color: currentColor, $overlay: false) {
    @if $overlay {
        $bg: var(--rgb-overlay);
    }

    &:not(:disabled):hover,
    &.hover {
        border-color: $border-color;
        background-image: linear-gradient(rgba($bg, 0.05), rgba($bg, 0.05));
        box-shadow: 0 rem(4) rem(12) rem(-4) $shadow-color;
        text-decoration: none; // override for link buttons
    }
}
@mixin button-active($bg, $fg: null, $shadow-color: null, $overlay: false) {
    @if $overlay {
        $bg: var(--rgb-overlay);
    }

    &:not(:disabled):active,
    &.active {
        background-image: linear-gradient(rgba($bg, 0.1), rgba($bg, 0.1));
        box-shadow: 0 rem(4) rem(4) rem(-4) $shadow-color;

        @if $fg {
            color: #{$fg};
        }
    }
}
@mixin button-disabled($bg-color, $border-color: null, $fg: null) {
    &:disabled {
        border-color: $border-color;
        background-color: $bg-color;

        @if $fg {
            color: #{$fg};
        }

        cursor: not-allowed;
    }
}
@mixin button-focus() {
    &:focus-visible {
        &:not(:disabled, :active),
        &.focus {
            @include element-focus-ring;
        }
    }
}
@mixin button-hidden-label() {
    &.g-button-size-small {
        padding: 0 rem(2);
    }

    &.g-button-size-medium {
        padding: 0 rem(8);
    }

    & .g-button-label {
        @include sr-only;
    }
}
@mixin button-full-width() {
    width: 100%;
}
@mixin button-merged-borders() {
    &:not(:first-of-type) {
        margin-left: rem(-2);
        @include border-left-radius(0);
    }

    &:not(:last-of-type) {
        margin-right: rem(0);
        @include border-right-radius(0);
    }
}

// ─────────────────────────────────────────────────────────────────────────────
// resets
// ─────────────────────────────────────────────────────────────────────────────

.g-button {
    margin: 0;
    padding: 0;
    border-radius: 0;
    outline: none;
    background: none;
    color: var(--color, inherit); // used for inverted unstyled buttons
    font-family: inherit;
    line-height: 0;
    appearance: none;
}

// ─────────────────────────────────────────────────────────────────────────────
// defaults
// ─────────────────────────────────────────────────────────────────────────────

/* stylelint-disable-next-line no-duplicate-selectors */
.g-button {
    $label-icon-gutter-size: rem(8);
    $label-pill-gutter-size: rem(6);

    @include button-focus;

    display: inline-flex;
    align-items: center;
    justify-content: center;
    max-width: 100%;
    transition-property: background-color, border-color, $input-transition-property;
    transition-duration: $input-transition-duration;
    border: rem(2) solid transparent;
    border-radius: rem(4);
    font-weight: $typo-x-font-weight-semi-bold;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;

    &-icon {
        flex-shrink: 0;
        transition-property: transform;
        transition-duration: 250ms;

        & + .g-button-label {
            margin-left: $label-icon-gutter-size;
        }

        &-right {
            order: 1;

            & + .g-button-label {
                margin-right: $label-icon-gutter-size;
                margin-left: 0;
            }
        }
    }

    &[aria-expanded='true'] {
        .g-button-icon.ico-caret-down {
            transform: rotate(180deg);
        }
    }

    &-label {
        @include ellipsis;

        // breathing room for ascenders & descenders:
        padding-top: rem(2);
        padding-bottom: rem(2);
    }

    .badge-pill, // DEPRECATED
    .g-pill {
        margin-right: 0;
        margin-left: $label-pill-gutter-size;
    }

    .g-button-icon-right {
        ~ .badge-pill, // DEPRECATED
        ~ .g-pill {
            margin-right: $label-icon-gutter-size;
            margin-left: $label-pill-gutter-size - $label-icon-gutter-size;
        }
    }

    &:disabled {
        .badge-pill, // DEPRECATED
        .g-pill {
            filter: grayscale(1);
            opacity: 0.1;
        }
    }
}

// ─────────────────────────────────────────────────────────────────────────────
// variants
// ─────────────────────────────────────────────────────────────────────────────

.g-button-variant {
    &-primary {
        background-color: var(--color-blue-300);
        color: var(--color-white);

        @include button-hover(var(--rgb-black), null, rgba(var(--rgb-blue-300), 0.6));
        @include button-active(
            var(--rgb-black),
            var(--color-white),
            rgba(var(--color-blue-300), 0.6)
        );
        @include button-disabled(rgba(var(--rgb-gray-300), 0.5));
    }

    &-secondary {
        border-color: var(--g-color-border-primary);
        color: var(--color-blue-300);

        @include button-hover(var(--rgb-blue-300), null, rgba(var(--rgb-black), 0.3));
        @include button-active(var(--rgb-blue-300), null, rgba(var(--rgb-black), 0.3));
        @include button-disabled(null, var(--color-gray-200), var(--g-color-text-disabled));
    }

    &-tertiary {
        color: var(--color-blue-300);

        @include button-hover(var(--rgb-blue-300), null, var(--color-transparent));
        @include button-active(var(--rgb-blue-300), null, var(--color-transparent));
        @include button-disabled(null, null, var(--g-color-text-disabled));
    }

    &-danger {
        border-color: rgba(var(--rgb-ui-red), 0.5);
        color: var(--color-ui-red);

        @include button-hover(var(--rgb-ui-red), null, rgba(var(--rgb-ui-red), 0.4));
        @include button-active(var(--rgb-ui-red), null, rgba(var(--rgb-ui-red), 0.6));
        @include button-disabled(null, var(--color-gray-200), var(--g-color-text-disabled));
    }
}

.g-inverted {
    &.g-button-variant,
    .g-button-variant {
        &-primary:not(.g-no-inverted) {
            background-color: var(--color-white);
            color: var(--color-blue-300);

            @include button-hover(var(--rgb-black), null, rgba(var(--rgb-black), 0.45));
            @include button-active(
                var(--rgb-black),
                var(--color-blue-300),
                rgba(var(--rgb-black), 0.45)
            );
            @include button-disabled(rgba(var(--rgb-white), 0.5), null, var(--color-bg));
        }

        &-secondary:not(.g-no-inverted) {
            border-color: var(--g-color-border-inverted-secondary);
            color: var(--color-white);

            @include button-hover(
                var(--rgb-black),
                rgba(var(--rgb-white), 0.7),
                rgba(var(--rgb-black), 0.3)
            );
            @include button-active(
                var(--rgb-black),
                var(--color-white),
                rgba(var(--rgb-black), 0.3)
            );
            @include button-disabled(
                null,
                rgba(var(--rgb-white), 0.25),
                var(--g-color-text-inverted-disabled)
            );
        }

        &-tertiary:not(.g-no-inverted) {
            color: var(--color-white);

            @include button-hover(var(--rgb-black), null, transparent, true);
            @include button-active(var(--rgb-black), null, transparent, true);
            @include button-disabled(null, null, var(--g-color-text-inverted-disabled));
        }

        &-danger:not(.g-no-inverted) {
            border-color: var(--g-color-border-inverted-secondary);
            color: var(--color-white);

            @include button-hover(
                var(--rgb-black),
                rgba(var(--rgb-white), 0.7),
                rgba(var(--rgb-black), 0.3)
            );
            @include button-active(
                var(--rgb-black),
                var(--color-white),
                rgba(var(--rgb-black), 0.3)
            );
            @include button-disabled(
                null,
                rgba(var(--rgb-white), 0.25),
                var(--g-color-text-inverted-disabled)
            );
        }
    }
}

// ─────────────────────────────────────────────────────────────────────────────
// sizes
// ─────────────────────────────────────────────────────────────────────────────

.g-button-size {
    &-medium {
        min-height: rem(40);
        padding: 0 rem(14);
        font-size: rem(16);
        line-height: rem(24);

        & .g-button-icon {
            width: rem(24);
            height: rem(24);
        }

        & .g-spinner {
            --size: #{rem(24)};
        }
    }

    &-small {
        min-height: rem(24);
        padding: 0 rem(6);
        font-size: rem(14);
        line-height: rem(16);

        & .g-spinner {
            --size: #{rem(16)};
        }
    }
}

// ─────────────────────────────────────────────────────────────────────────────
// hidden label
// ─────────────────────────────────────────────────────────────────────────────

.g-button-hidden-label {
    &-below-sm {
        @include media-breakpoint-down(xs) {
            @include button-hidden-label;
        }
    }

    &-below-md {
        @include media-breakpoint-down(sm) {
            @include button-hidden-label;
        }
    }

    &-always {
        @include button-hidden-label;
    }
}

// ─────────────────────────────────────────────────────────────────────────────
// full width
// ─────────────────────────────────────────────────────────────────────────────

.g-button-full-width {
    &-below-sm {
        @include media-breakpoint-down(xs) {
            @include button-full-width;
        }
    }

    &-below-md {
        @include media-breakpoint-down(sm) {
            @include button-full-width;
        }
    }

    &-always {
        @include button-full-width;
    }
}

// ─────────────────────────────────────────────────────────────────────────────
// spinner
// ─────────────────────────────────────────────────────────────────────────────

.g-button-loading {
    justify-content: center !important; // stylelint-disable-line declaration-no-important

    > .g-spinner {
        position: absolute;
        margin: auto;

        ~ .g-button-label,
        ~ .g-button-icon,
        ~ .badge-pill, // DEPRECATED
        ~ .g-pill {
            visibility: hidden;
        }
    }
}

// ─────────────────────────────────────────────────────────────────────────────
// rounded button
// ─────────────────────────────────────────────────────────────────────────────

.g-button-rounded {
    border-radius: 50%;
}

// ─────────────────────────────────────────────────────────────────────────────
// unstyled button
// ─────────────────────────────────────────────────────────────────────────────

.g-button-unstyled {
    padding: 0;
    border-width: 0;

    .g-button-label {
        display: inline-flex;
        padding: 0;
        overflow: visible;
    }
}

// ─────────────────────────────────────────────────────────────────────────────
// button group & segmented buttons
// ─────────────────────────────────────────────────────────────────────────────

.g-group-button {
    .g-button {
        margin: ($button-spacing-vertical * 0.5) 0;

        &.g-button-full-width-never:not(:last-child) {
            margin-right: $button-spacing-horizontal * 0.5;
        }

        @include media-breakpoint-up(sm) {
            margin: ($button-spacing-vertical * 0.5) ($button-spacing-horizontal * 0.5);

            &.g-button-size-small {
                &:not(:last-child) {
                    margin-right: 0;
                }
            }
        }

        @include media-breakpoint-down(xs) {
            &.g-button-variant-primary {
                order: -1;
            }
        }
    }

    &.g-group-button-segmented {
        .g-button {
            @include ellipsis;

            z-index: 1;

            &:disabled {
                z-index: 0;
            }

            &:not(.g-button-full-width-never) {
                @include media-breakpoint-down(xs) {
                    &:not(:first-of-type) {
                        margin-top: rem(-2);
                        @include border-top-radius(0);
                    }

                    &:not(:last-of-type) {
                        margin-bottom: rem(0);
                        @include border-bottom-radius(0);
                    }
                }
            }

            &.g-button-full-width-never {
                @include button-merged-borders;
            }

            @include media-breakpoint-up(sm) {
                @include button-merged-borders;
            }

            &:not(:disabled)[aria-pressed='true'] {
                z-index: 2;
                border-color: var(--g-color-interactive-primary);
                background-color: var(--g-color-overlay-secondary);
            }
        }

        &:not(.g-group-button-inline) {
            > .g-button {
                flex-grow: 1;
            }
        }
    }
}

/* stylelint-disable-next-line no-duplicate-selectors */
.g-inverted {
    .g-group-button {
        &.g-group-button-segmented {
            .g-button {
                &:not(:disabled)[aria-pressed='true'] {
                    border-color: var(--g-color-border-inverted-primary);
                }
            }
        }
    }
}

// ─────────────────────────────────────────────────────────────────────────────
// input prepends & appends
// ─────────────────────────────────────────────────────────────────────────────

.input-group {
    .g-button {
        // makes currently "selected" button appear above its neighbours
        &:not(:disabled) {
            &:hover,
            &:not(:active):focus {
                z-index: 1;
            }

            &.g-button-variant-secondary {
                // prevents border color removal on focus
                border-color: $input-bordercolor !important; // stylelint-disable-line declaration-no-important
            }

            @include element-focus('', true, true);
        }
    }

    & > .input-group-prepend {
        > .g-button {
            @include border-right-radius(0);
        }

        &:not(:first-child) > .g-button {
            margin-left: rem(-1);
            @include border-left-radius(0);
        }
    }

    & > .input-group-append {
        > .g-button {
            @include border-left-radius(0);
        }

        &:not(:last-child) > .g-button {
            margin-right: rem(-1);
            @include border-right-radius(0);
        }
    }
}
