// DEPRECATED

.progress {
    @include typo(p-micro);

    height: rem(20);
    border-radius: rem(4);
    background-color: rgba($color-blue-400, 0.1);
    line-height: rem(19); // to get rid of uneven alignment

    .progress-bar {
        height: inherit;
        padding-right: rem(8);
        padding-left: rem(8);
        background-color: $color-blue-300;
        font-weight: $typo-x-font-weight-semi-bold;
        line-height: inherit;
        text-align: left;
    }

    &-medium {
        height: rem(6);
        border-radius: rem(3);
        line-height: rem(6);
    }

    &-small {
        height: rem(2);
        border-radius: rem(1);
        line-height: rem(2);
    }
}
