.g-no-ellipsis {
    @include noellipsis;
}

// used around hover-areas to auto-fit its size
.g-hover-box {
    display: inline-block;
}

&.g-portal {
    z-index: z-index-level(portal);
    pointer-events: auto;
}

.sr-only {
    @include sr-only;
}

.clipboard {
    user-select: text;
}

.no-clipboard {
    user-select: none;

    @include at-namespace('.g-is-copying') {
        visibility: hidden;
    }
}

[tabindex='-1'] {
    &:focus {
        outline: none;
    }
}
