@mixin g-card-product-slot {
    .g-card-product-body,
    .g-card-product-indicator,
    .g-card-product-footer {
        @content;
    }
}

@mixin offset-indicator-badge {
    .g-card-product-indicator-badge {
        --card-padding-horizontal: #{$productcard-padding-reduced};
        --card-padding-vertical: #{$productcard-padding-reduced - rem(4)}; // substract heading/paragragh padding (i.e. fake-margin)
    }
}

.g-card-product {
    --card-padding-horizontal: #{$productcard-padding};
    --card-padding-vertical: #{$productcard-padding - rem(4)}; // substract heading/paragragh padding (i.e. fake-margin)

    display: flex;
    position: relative;
    flex-direction: column;
    max-width: 100%;
    margin-top: $spacing-vertical-half;
    margin-bottom: $spacing-vertical-half;
    overflow: hidden;
    border-radius: $card-border-radius;
    background-color: var(--g-color-surface-primary);

    @include container-shadow;

    @include g-card-product-slot {
        display: flex;
        flex-direction: column;
        padding: var(--card-padding-vertical) var(--card-padding-horizontal);
    }

    .g-card-product-title {
        @include typo-offset(h3);
        @include typo(h3);

        flex-shrink: 0;
        font-weight: $typo-h-font-weight;
    }

    &.g-card-product-variant-plugin {
        @include media-breakpoint-up(md) {
            --card-padding-vertical: #{$functioncard-spacing};
            --card-padding-horizontal: #{$functioncard-padding};

            flex-direction: row;

            @include offset-indicator-badge;

            @include g-card-product-slot {
                justify-content: center;
            }

            .g-card-product-indicator {
                flex-basis: $functioncard-horizontal-indicator-width-regular;
                min-width: 0;
                margin-bottom: 0;
            }

            .g-card-product-body {
                --card-padding-vertical: #{$functioncard-spacing-prominent};
            }

            .g-card-product-footer {
                flex-basis: $productcard-horizontal-button-width;
                min-width: 0;
                margin-top: 0;
                padding-top: $functioncard-spacing;
                padding-left: 0;
                text-align: center;

                .g-group-button {
                    order: -1;
                    padding-top: rem(10);
                    padding-bottom: rem(10);
                }

                .btn, // DEPRECATED (added for ActivatedPlugin backwards-compat)
                .g-button {
                    width: 100%;
                    padding-right: rem(5);
                    padding-left: rem(5);
                }
            }
        }
    }

    &.g-card-product-variant-teaser,
    &.g-card-product-variant-large-teaser {
        height: rem(280);

        .g-card-product-title {
            a::after {
                content: '';
                @include cover-parent($z-index: z-index-level(base, 1));
            }
        }

        .g-card-product-body {
            padding-bottom: 0;
        }

        .g-card-product-content {
            padding-bottom: var(--card-padding-vertical);
            overflow: hidden;
            mask-image: linear-gradient(
                to top,
                $mask-content-hidden rem(-1),
                $mask-content-visible var(--card-padding-vertical)
            );
        }

        .g-card-product-footer {
            margin-top: 0;
            padding-top: rem(4);
        }
    }

    @include media-breakpoint-down(sm) {
        @include offset-indicator-badge;
    }

    &.g-card-product-variant-teaser {
        @include media-breakpoint-up(md) {
            @include offset-indicator-badge;
        }
    }

    &.g-card-product-variant-large-teaser {
        @include media-breakpoint-up(md) {
            height: rem(412);

            .g-card-product-indicator {
                flex-basis: rem(244);

                &-custom {
                    &:not(.g-card-product-indicator-cover) {
                        img,
                        svg {
                            max-height: rem(128); // not $productcard-icon-size-large, on purpose
                        }
                    }
                }
            }

            .g-card-product-icon {
                width: $productcard-icon-size-large;
                height: $productcard-icon-size-large;
            }
        }
    }

    .g-card-product-indicator {
        position: relative;
        flex: 0 0 rem(112);
        align-items: center;
        justify-content: center;
        order: -1;
        margin-bottom: 0;
        background-color: var(--color-bg, var(--g-color-fill-primary));
        color: var(--color-fg, var(--g-color-fill-inverted));

        @media (forced-colors: active) {
            background-color: CanvasText !important; // stylelint-disable-line declaration-no-important

            .g-card-product-icon {
                color: Canvas !important; // stylelint-disable-line declaration-no-important
            }
        }

        &-custom {
            &:not(.g-card-product-indicator-cover) {
                img,
                svg {
                    max-height: $productcard-icon-size-default;
                }
            }
        }

        &-cover {
            img,
            svg {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                max-width: none;
                height: 100%;
                max-height: none;
                object-fit: cover;
                object-position: 50%;
            }
        }
    }

    .g-card-product-body {
        flex: 1 1;
        overflow: hidden;
    }

    .g-card-product-footer {
        flex: 0 0;

        @include media-breakpoint-down(sm) {
            margin-top: -$functioncard-spacing;
            padding-top: 0;
        }

        .g-group-button {
            display: flex;
            flex-wrap: wrap;
            gap: rem(15) rem(20);
            margin: 0;
            padding: rem(20) 0 rem(4);

            @include media-breakpoint-only(sm) {
                justify-content: center;
                padding-bottom: rem(14);
            }
        }

        .btn, // DEPRECATED (added for ActivatedPlugin backwards-compat)
        .g-button {
            margin: 0;
        }
    }
}

.g-card-product-title {
    margin-right: -$element-focus-size;
    margin-left: -$element-focus-size;
    padding-right: $element-focus-size;
    padding-left: $element-focus-size;

    &,
    a {
        @include ellipsis;
    }

    a {
        display: block;
        width: max-content;
        max-width: 100%;
        color: inherit;
        font-weight: inherit;
    }
}

.g-card-product-icon {
    width: $productcard-icon-size-default;
    height: $productcard-icon-size-default;
}

.g-card-product-indicator-badge {
    position: absolute;
    top: 0;
    right: 0;
    max-width: calc(100% - 2 * var(--card-padding-horizontal));
    margin: var(--card-padding-vertical) var(--card-padding-horizontal) 0 0;
    padding: 0;
}

.g-card-product-price {
    .g-card-product-active {
        position: relative;
        top: rem(0.5);
        margin-right: em(2);
        border: rem(1) solid var(--color-bg);
        border-radius: 50%;
    }
}

// DEPRECATED (added for ActivatedPlugin backwards-compat)
.g-card-function-plugin {
    &.g-card {
        display: flex;
    }

    .g-card-product-footer {
        p {
            @include typo(small);
        }
    }
}
