// the huge cards used to describe a product (not the checkout pages)

$product-color-headline1: $color-blue-300;
$product-color-headline2: $color-blue-400;
$product-color-text-faded: $color-gray-300;
$product-color-background-grey: $color-gray-100;
$product-color-separator: $color-separator;

$product-h1-offset-top: rem(10);
$product-h1-offset-bottom: rem(6);
$product-h2-offset-top: rem(6);
$product-h2-offset-bottom: rem(5);

$product-spacing: rem(20);
$product-spacing-h1h2: $product-spacing;
$product-spacing-large: rem(40);

$product-spacing-horizontal: rem(30);

$product-header-spacing: rem(40);
$product-body-spacing: rem(40);
$product-footer-spacing: rem(30);
$product-footer-min-height: rem(106);

.g-card.g-card-product {
    &:not(.g-card-function-plugin) {
        border-color: $product-color-separator !important; // stylelint-disable-line declaration-no-important

        .card-body, // DEPRECATED
        .g-card-body {
            padding-right: $product-spacing-horizontal;
            padding-left: $product-spacing-horizontal;

            @include media-breakpoint-up(lg) {
                padding-right: rem(140);
                padding-left: rem(140);
            }
        }

        .g-faded {
            color: $product-color-text-faded;
        }

        video,
        audio {
            display: block;
            width: 100%;
            margin: 0 auto;
        }
    }

    &.g-card-product-header {
        .card-body, // DEPRECATED
        .g-card-body {
            padding-top: $product-header-spacing - $spacing-vertical-half;
            padding-bottom: $product-header-spacing - $spacing-vertical-half;
            text-align: center;

            h1,
            .g-h1 {
                padding-top: $spacing-vertical-half - $typo-huge-offset-top;
                padding-bottom: $spacing-vertical-half - $typo-huge-offset-bottom;
            }

            h2,
            .g-h2 {
                padding-top: $spacing-vertical-half - $typo-h2-offset-top;
                padding-bottom: $spacing-vertical-half - $typo-h2-offset-bottom;
            }

            h3,
            .g-h3 {
                padding-top: $spacing-vertical-half - $typo-h3-offset-top;
                padding-bottom: $spacing-vertical-half - $typo-h3-offset-bottom;
            }

            h1 + p,
            .g-h1 + p,
            h2 + p,
            .g-h2 + p,
            h3 + p,
            .g-h3 + p {
                padding-top: $spacing-vertical-half - $typo-p-offset-top + rem(20);
            }
        }
    }

    &.g-card-product-body {
        .card-body, // DEPRECATED
        .g-card-body {
            padding-top: $product-body-spacing - $spacing-vertical-half;
            padding-bottom: $product-body-spacing - $spacing-vertical-half;
        }
    }

    &.g-card-product-body-banner-light,
    &.g-card-product-body-banner-dark {
        height: rem(235);
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;

        h1,
        .g-h1 {
            padding-top: $spacing-vertical-half - $typo-huge-offset-top;
            padding-bottom: $spacing-vertical-half - $typo-huge-offset-bottom;
        }

        h2,
        .g-h2 {
            padding-top: $spacing-vertical-half - $typo-h2-offset-top;
            padding-bottom: $spacing-vertical-half - $typo-h2-offset-bottom;
        }

        h3,
        .g-h3 {
            padding-top: $spacing-vertical-half - $typo-h3-offset-top;
            padding-bottom: $spacing-vertical-half - $typo-h3-offset-bottom;
        }

        .card-body, // DEPRECATED
        .g-card-body {
            @include flex-column-left-center;

            min-height: 100%;
        }
    }

    &.g-card-product-body-banner-dark {
        background-color: $color-gray-300;

        .card-body, // DEPRECATED
        .g-card-body {
            p,
            h1,
            h2,
            .h1,
            .h2 {
                color: $color-white;
            }
        }
    }

    &.g-card-product-body-banner-light {
        .card-body, // DEPRECATED
        .g-card-body {
            p,
            h1,
            h2,
            .h1,
            .h2 {
                color: $typo-color-text;
            }
        }
    }

    &.g-card-product-body-table {
        h2,
        .g-h2,
        h3,
        .g-h3 {
            text-align: center;
        }

        dt {
            font-weight: inherit;
        }

        @include media-breakpoint-up(md) {
            dd {
                text-align: right;
            }
        }
    }

    &.g-card-product-footer {
        min-height: $product-footer-min-height;

        .card-body, // DEPRECATED
        .g-card-body {
            padding-top: $product-footer-spacing - $spacing-vertical-half;
            padding-bottom: $product-footer-spacing - $spacing-vertical-half;
            text-align: center;
        }
    }
}

// disable some lines around banner images
.g-card-product-body-banner-dark,
.g-card-product-body-banner-dark + .g-card,
.g-card-product-body-banner-light,
.g-card-product-body-banner-light + .g-card {
    border-top: 0 !important; // stylelint-disable-line declaration-no-important
}
