/* You can add global styles to this file, and also import other style files */
// assets
@import 'react-dates/lib/css/_datepicker.css';
@import '@george-labs.com/design-system/sass/bootstrap';
@import '@george-labs.com/design-system/sass/gds-main';
@import '@george-labs.com/design-system/sass/gds-store';
@import '@george-labs.com/design-system/dist/gds-components.min.css';
// only need for map SASS spacers to CSS Variables
@import 'bootstrap/scss/functions';
@import 'bootstrap/scss/variables';

html,
body,
#root,
.g-store {
  height: 100vh;
}

.fixed-width {
  max-width: 1082px;
  margin: 0 auto !important;
}

h4 {
  font-size: 1.125rem !important;
  line-height: 24px !important;
}

:root {
  --spacer1: #{map-get($spacers, 1)};
  --spacer2: #{map-get($spacers, 2)};
  --spacer3: #{map-get($spacers, 3)};
  --spacer4: #{map-get($spacers, 4)};
  --spacer5: #{map-get($spacers, 5)};
}

.g-bootstrap.g-store.g-portal {
  display: inline;
}
