@mixin input {
    @include typo(p);

    height: unset;
    transition: border-color 150ms ease-in-out,
        $input-transition-property $input-transition-duration;
    background: $color-white;
    background-image: $input-fake-shadow;
    color: $input-color;
    font-family: inherit;
    font-weight: $typo-x-font-weight-regular;
}

@mixin input-hover {
    border-color: $input-bordercolor-hover;
    outline: none;
}

@mixin input-focus {
    z-index: 5;
    outline: none;
    color: $input-color;
    @include element-focus-ring(true);
}

@mixin input-disabled {
    &,
    .btn {
        border-color: $input-disabled-border !important; // stylelint-disable-line declaration-no-important
        opacity: 1;
        background-color: $input-disabled-background !important; // stylelint-disable-line declaration-no-important
        background-image: none !important; // stylelint-disable-line declaration-no-important
        box-shadow: none;
        color: $input-disabled-color !important; // stylelint-disable-line declaration-no-important
        cursor: not-allowed;
    }
}

@mixin form-control {
    padding: $input-control-padding rem(10);
    transition: (
        border-color 150ms ease-in-out,
        $input-transition-property $input-transition-duration
    );
    border: $input-border-width solid $input-bordercolor;
    border-radius: rem(4);
    caret-color: $color-blue-300;

    &:not(:disabled) {
        &:not([readonly]) {
            &:hover {
                @include input-hover;
            }
        }

        &:focus {
            @include input-focus;
        }
    }

    &:disabled {
        &,
        & ~ .input-group-prepend,
        & ~ .input-group-append {
            @include input-disabled;

            .btn {
                opacity: 1;
            }
        }
    }
}
