// --- contact summary ---------------------------------------------------------
//     see also product summary (.g-product-info)

.g-contact-info {
    min-height: rem(64);

    &-extended {
        min-height: rem(104);
    }

    .g-amount {
        @include increase-specificity(1) {
            font-size: rem(16);
            line-height: rem(20);
        }
    }

    .g-avatar {
        @include increase-specificity(1) {
            color: $color-gray-300;
        }
    }
}
