.g-input-control {
    width: $g-custom-control-indicator-size;
    height: $g-custom-control-indicator-size;
    transition: border-color $input-transition-duration, box-shadow $input-transition-duration;
    border: rem(1) solid $input-bordercolor;
    border-radius: rem(2);
    outline: none;
    background: $color-white $input-fake-shadow;
    cursor: pointer;
    appearance: none;

    &:hover {
        border-color: $input-bordercolor-hover;
    }

    &:focus-visible {
        @include element-focus-ring;
    }

    &:not(:disabled):active {
        box-shadow: inset 0 0 0 $g-custom-control-indicator-size rgba(0, 0, 0, 25%);
    }

    &:checked {
        border-color: $input-button-color;
        background: $input-button-color svg-ico-checkbox($color-white);
    }

    &:indeterminate {
        background-image: svg-ico-minus($color-white);
    }

    &:disabled {
        border-color: $input-disabled-color;
        cursor: not-allowed;

        &:checked {
            background: $color-white svg-ico-checkbox($input-disabled-border);
        }

        &:indeterminate {
            background: $color-white svg-ico-minus($input-disabled-border);
        }
    }

    &[type='radio'] {
        border-radius: 50%;

        &:checked {
            background-image: svg-ico-circle($color-white);
        }

        &:disabled:checked {
            background-image: svg-ico-circle($input-disabled-border);
        }
    }
}

// --- toggle icon -------------------------------------------------------------

.g-toggle {
    display: flex;
    position: relative;
    flex-direction: column;

    &-label {
        margin: 0;
        padding: rem(16);
        overflow: hidden;
        transition: background-color 250ms, border-color 250ms,
            $input-transition-property $input-transition-duration;
        border: rem(2) solid $color-gray-300;
        border-radius: rem(4);
        cursor: pointer;
    }

    &-text {
        display: block;
        margin-top: rem(8);
    }

    &-description {
        @include typo(p-micro, $lineHeight: false);

        display: block;
        margin-top: rem(12);
        font-weight: $typo-p-font-weight-regular;
        line-height: rem(16);
    }

    &-input {
        position: absolute;
        inset: rem(16) rem(16) auto auto;

        &:hover ~ .g-toggle-label {
            border-color: mix($color-gray-400, $color-white, 60%);
            background-color: rgba($color-blue-300, 0.06);
            box-shadow: 0 rem(4) rem(12) rem(-4) rgba($color-black, 0.3);

            &:active {
                box-shadow: 0 rem(4) rem(4) rem(-4) rgba($color-black, 0.3);
            }
        }

        &:checked ~ .g-toggle-label {
            border-color: $color-blue-300;
            background-color: rgba($color-blue-300, 0.05);
        }

        &:checked:hover ~ .g-toggle-label {
            background-image: linear-gradient(
                rgba($color-blue-300, 0.05),
                rgba($color-blue-300, 0.05)
            );
        }

        &:focus-visible {
            box-shadow: none;

            & ~ .g-toggle-label {
                @include element-focus-ring;
            }
        }

        &:disabled {
            & ~ .g-toggle-label {
                border-color: $input-disabled-border;
                background-color: $input-disabled-background;
                color: $input-disabled-color;
                cursor: not-allowed;
            }

            &:hover ~ .g-toggle-label {
                box-shadow: none;
            }

            &:checked ~ .g-toggle-label {
                background-color: $color-gray-200;
            }
        }
    }
}

// --- toggle image ------------------------------------------------------------

.g-toggle-flexible {
    .g-toggle-label {
        display: flex;
        flex-direction: column;
        min-height: rem(188);
    }

    .g-toggle-image {
        width: auto;
        max-width: rem(110);
        height: auto;
        max-height: rem(100);
        margin: auto auto auto 0;
    }

    .g-toggle-input:disabled ~ .g-toggle-label {
        .g-toggle-image {
            filter: grayscale(100%) opacity(50%);
        }
    }
}

// --- toggle text-only --------------------------------------------------------

.g-toggle-text-only {
    .g-toggle-label {
        padding-right: rem(36);
    }

    .g-toggle-text {
        margin: 0;
    }

    &:not(.g-toggle-with-description) {
        .g-toggle-input {
            top: rem(22);
        }
    }
}

// --- toggle badge -----------------------------------------------------------

.g-toggle-badge {
    // DEPRECATED
    &.g-toggle-badge-large {
        --padding: #{rem(10)} #{rem(12)};
        --icon-padding: #{rem(32)};
        --icon-top: #{rem(12)};
        --icon-right: #{rem(10)};
    }

    &.g-toggle-badge-medium {
        --padding: #{rem(8)} #{rem(12)};
        --icon-padding: #{rem(32)};
        --icon-top: #{rem(10)};
        --icon-right: #{rem(10)};
    }

    &.g-toggle-badge-small {
        --padding: #{rem(4)} #{rem(8)};
        --icon-padding: #{rem(24)};
        --icon-top: #{rem(6)};
        --icon-right: #{rem(6)};
    }

    display: inline-flex;

    &:not(:last-of-type) {
        margin: 0 rem(8) rem(8) 0;
    }

    .g-toggle-label {
        padding: var(--padding);
    }

    .g-toggle-input {
        display: none;

        &:checked ~ .g-toggle-label {
            padding-right: var(--icon-padding);

            &::after {
                @include background-svg(svg-ico-checkmark($color-white));

                content: '';
                position: absolute;
                top: var(--icon-top);
                right: var(--icon-right);
                width: rem(16);
                height: rem(16);
                border-radius: 50%;
                background-color: $color-blue-300;
                background-size: rem(10);
            }
        }
    }

    .g-toggle-text {
        height: initial;
        margin: 0;
        font-size: map-get($typo-font-size, small);
        line-height: rem(16);
    }
}
