$icon-size-default: rem(16);

// the following icon size helpers are available as mixins AND as classes, since SASS sometimes
// only allows only mixins (e.g. in media queries)

@mixin g-image-core {
    width: var(--size);
    min-width: var(--size);
    height: var(--size);
    font-size: var(--size);
    line-height: var(--size);
}

@mixin g-image-size($size, $suppress-color: false) {
    --size: #{$size};
    @include g-image-core; // this is needed in every modifier until we have a migrated to a `.g-image.g-image-${size}` combo
    @if $suppress-color {
        color: $color-sanity !important; // stylelint-disable-line declaration-no-important
    }
}

@mixin g-image-16 {
    @include g-image-size(rem(16));
}
@mixin g-image-20 {
    @include g-image-size(rem(20)); // DEPRECATED set $suppress-color: true in GDS v3
}
@mixin g-image-24 {
    @include g-image-size(rem(24));
}
@mixin g-image-28 {
    @include g-image-size(rem(28), true);
}
@mixin g-image-32 {
    @include g-image-size(rem(32), true);
}
@mixin g-image-40 {
    @include g-image-size(rem(40), true);
}
@mixin g-image-48 {
    @include g-image-size(rem(48));
}
@mixin g-image-64 {
    @include g-image-size(rem(64));
}
@mixin g-image-96 {
    @include g-image-size(rem(96));
}
@mixin g-image-128 {
    @include g-image-size(rem(128));
}

.g-image {
    max-width: 100%;
}

.g-image-16 {
    @include g-image-16;
}

.g-image-20 {
    @include g-image-20;
}

.g-image-24 {
    @include g-image-24;
}

.g-image-28 {
    @include g-image-28;
}

.g-image-32 {
    @include g-image-32;
}

.g-image-40 {
    @include g-image-40;
}

.g-image-48 {
    @include g-image-48;
}

.g-image-64 {
    @include g-image-64;
}

.g-image-96 {
    @include g-image-96;
}

.g-image-128 {
    @include g-image-128;
}

svg {
    @include g-image-size($icon-size-default);

    display: inline-block;
    fill: currentColor;
}

li,
p,
a {
    svg {
        vertical-align: em(-2);
    }
}

li,
p {
    svg:where(.g-image-24) {
        vertical-align: em(-4);
    }
}
