@import 'color-generated';

$color-sanity: #ff4aff;
$color-complement-light: $color-white;
$color-complement-dark: $color-blue-400; // paragraph dark
$rgb-overlay-light: 255, 255, 255;
$rgb-overlay-dark: 0, 0, 0;

$color-separator: $color-gray-200;

$colored-stripe-size: rem(5);

$color-background-backdrop: rgba($color-blue-400, 0.3);
$shadow-modal: 0 rem(6.4) rem(92.8) rgba($color-black, 0.3);

$color-shadow-dropdown: rgba($color-black, 0.15);
$shadow-dropdown: 0 rem(1) rem(4) rem(1) $color-shadow-dropdown;
