// TODO remove in GDS v3
.badge {
    // DEPRECATED
    position: static;
    text-align: left;
}

// TODO remove in GDS v3
.badge:not(.badge-pill, .g-badge-category) {
    // DEPRECATED
    border-color: transparent;
}

.badge, // DEPRECATED
.g-badge,
.g-pill {
    display: inline-block;
    font-size: rem(12);
    font-weight: $typo-x-font-weight-semi-bold;
}

.badge:not(.badge-pill), // DEPRECATED
.g-badge {
    @include ellipsis;

    max-width: 100%;
    padding: rem(1) rem(8);
    border: rem(1) solid transparent;
    border-radius: rem(10);
    line-height: rem(16);
    vertical-align: middle;
}

.badge-pill, // DEPRECATED
.g-pill {
    padding: rem(2) rem(5) rem(3);
    border-radius: rem(8);
    line-height: rem(11);
    vertical-align: baseline;
    white-space: nowrap;
}

@mixin g-badge($fg, $bg: null) {
    background-color: $bg;
    color: $fg;
}

.g-badge-todo {
    @include g-badge(var(--g-color-info), var(--g-color-info-weak));

    &.g-badge-filled {
        @include g-badge(var(--g-color-text-inverted-primary), var(--g-color-info));
    }
}

.g-badge-state {
    @include g-badge(var(--color-ui-gray), var(--color-ui-gray-weak));

    &.g-badge-filled {
        @include g-badge(var(--g-color-text-inverted-primary), var(--color-ui-gray));
    }
}

.g-badge-error {
    @include g-badge(var(--g-color-error), var(--g-color-error-weak));

    &.g-badge-filled {
        @include g-badge(var(--g-color-text-inverted-primary), var(--g-color-error));
    }
}

.g-badge-warning {
    @include g-badge(var(--g-color-warning-strong), var(--g-color-warning-weak));

    &.g-badge-filled {
        @include g-badge(var(--g-color-text-inverted-primary), var(--g-color-warning-strong));
    }
}

.g-badge-success {
    @include g-badge(var(--g-color-success), var(--g-color-success-weak));

    &.g-badge-filled {
        @include g-badge(var(--g-color-text-inverted-primary), var(--g-color-success));
    }
}

.g-badge-category {
    @include g-badge(var(--color-ui-gray));

    border-color: currentColor;
}

@each $color
    in (
        'honey',
        'carrot',
        'berry',
        'rose',
        'lavender',
        'lila',
        'indigo',
        'pool',
        'teal',
        'lime',
        'forest',
        'stone'
    )
{
    .g-badge-#{$color} {
        @include g-badge(var(--color-#{$color}-strong), var(--color-#{$color}-weak));

        &.g-badge-filled {
            @include g-badge(var(--g-color-text-inverted-primary), var(--color-#{$color}));
        }
    }
}

// override honey filled badge for a11y reasons
.g-badge-honey.g-badge-filled {
    color: var(--g-color-text-primary);
}

.g-inverted {
    .g-badge-todo {
        @include g-badge(var(--g-color-info), var(--g-color-text-inverted-primary));
    }

    .g-badge-state {
        @include g-badge(var(--color-ui-gray), var(--g-color-text-inverted-primary));
    }

    .g-badge-error {
        @include g-badge(var(--g-color-error), var(--g-color-text-inverted-primary));
    }

    .g-badge-warning {
        @include g-badge(var(--g-color-warning-strong), var(--g-color-text-inverted-primary));
    }

    .g-badge-success {
        @include g-badge(var(--g-color-success), var(--g-color-text-inverted-primary));
    }

    .g-badge-category {
        @include g-badge(var(--g-color-text-inverted-primary));
    }

    .g-indicator {
        color: var(--g-color-text-inverted-primary);
    }
}

.g-badge-icon {
    svg {
        margin-right: rem(4);
        vertical-align: bottom;
    }
}

.g-indicator {
    width: rem(10) !important; // stylelint-disable-line declaration-no-important
    min-width: 0;
    height: rem(10) !important; // stylelint-disable-line declaration-no-important
    margin: 0 rem(6) rem(1) 0;
    overflow: visible;
}

.g-indicator-unread {
    color: var(--g-color-info);
}

.g-pill-default {
    @include g-badge(var(--g-color-fill-inverted), var(--g-color-text-primary));
}

.g-pill-unread {
    @include g-badge(var(--g-color-fill-inverted), var(--g-color-error));
}
