// message boxes in red, yellow and blue

%alert {
    // basic alert class - usually not used directly, but extended below with color
    @include increase-specificity(1) {
        flex-basis: auto;
        margin-top: $spacing-vertical-half;
        margin-bottom: $spacing-vertical-half;
        border-radius: rem(4);
        box-shadow: none;
    }

    .card-body, // DEPRECATED
    .g-card-body {
        padding-left: rem(15);

        @include flex-row-stretch-top;

        .g-card-alert-icon {
            --size: #{rem(16)};

            flex-grow: 0;
            flex-shrink: 0;
            margin-top: rem(7);
            margin-left: 0;
            float: left;
        }
    }

    .g-card-alert-data {
        @include flex-autoresize;

        margin-left: rem(10);

        p,
        %heading {
            @include typo(p-minor);

            margin-bottom: 0;
            padding-top: rem(4);
            padding-bottom: rem(4);
            border: 0 none;
            color: inherit;
            text-align: left;
        }

        div[class*='col-'] {
            // if there is a grid (in CTA versions), center them vertically
            @include flex-column-left-center; // TODO: refactor sizing logic after cta deprecation period

            button,
            .btn,
            .g-button,
            .g-group-button {
                width: 100%;
                margin-right: 0 !important; // stylelint-disable-line declaration-no-important
                margin-left: 0 !important; // stylelint-disable-line declaration-no-important
            }
        }
    }

    .g-card-close {
        position: absolute;
        z-index: z-index-level(base, 1);
        top: 0;
        right: 0;
        margin-top: rem(10);
        margin-right: rem(15);

        .g-btn-ui {
            position: relative;
            top: rem(-6);
            right: rem(-2);
        }
    }

    // TODO: remove after deprecation period
    &.g-card-alert-cta {
        .card-body, // DEPRECATED
        .g-card-body {
            @include flex-row-stretch-center;

            .g-card-alert-icon {
                width: rem(48);
                height: rem(48);
                margin-top: 0;
                font-size: rem(48);
                line-height: rem(48);
            }

            .g-card-alert-data {
                margin-left: rem(15);
            }

            @include media-breakpoint-down(sm) {
                // on small devices we place the icon on top of the message
                @include flex-column-center-center;

                .g-card-alert-icon {
                    margin-bottom: rem(10);
                }

                padding-top: rem(20);

                .g-card-alert-data {
                    margin-left: 0;
                }
            }
        }
    }
}

.g-card-alert-flag {
    img {
        height: rem(16);
        margin-top: rem(7);
        border-radius: rem(2);
    }
}

// --- colors for alerts -------------------------------------------------------

@mixin alert-colorize($fg, $bg) {
    &:not(.g-card-inverted) {
        border: rem(1) solid $fg !important; // stylelint-disable-line declaration-no-important
        background-color: $bg;

        .g-card-alert-icon {
            color: $fg;
        }
    }
}

.g-card {
    &.g-card-alert-info {
        @extend %alert;
        @include alert-colorize(var(--g-color-info), var(--g-color-info-weak));
    }

    &.g-card-alert-warning {
        @extend %alert;
        @include alert-colorize(var(--g-color-warning), var(--g-color-warning-weak));
    }

    &.g-card-alert-error {
        @extend %alert;
        @include alert-colorize(var(--g-color-error), var(--g-color-error-weak));
    }
}

.g-inverted {
    .g-card {
        &.g-card-alert-info,
        &.g-card-alert-warning,
        &.g-card-alert-error {
            border-color: transparent !important; // stylelint-disable-line declaration-no-important
            background: rgba(var(--rgb-black), 0.1);
            color: var(--g-color-text-inverted-primary);

            .g-card-alert-icon {
                color: inherit;
            }
        }
    }
}

// --- sizes for alerts --------------------------------------------------------

// stylelint-disable-next-line no-duplicate-selectors
.g-card {
    &.g-card-alert-small {
        .card-body, // DEPRECATED
        .g-card-body {
            padding: 0 rem(8);

            .g-card-alert-data {
                margin-left: rem(8);

                .g-group-button {
                    display: flex;
                    align-items: center;
                    justify-content: flex-end;

                    & > .g-button {
                        margin: 0;
                    }
                }

                .g-btn-outline-alert-error,
                .g-btn-outline-alert-warning,
                .g-btn-outline-alert-info {
                    @include typo(p-minor);

                    width: auto;
                    height: auto;
                    margin: 0;
                    padding: 0 rem(8);
                    border: none;
                    font-weight: $typo-x-font-weight-semi-bold;

                    @include media-breakpoint-down(sm) {
                        width: 100%;
                        margin-bottom: rem(8);
                    }
                }
            }
        }
    }

    // TODO: refactor sizing logic after cta deprecation period
    &.g-card-alert-medium {
        display: inline-flex;

        .g-card-alert-data {
            div[class*='col-']:first-of-type {
                justify-content: start;
            }
        }
    }

    &.g-card-alert-large {
        display: inline-flex;

        .card-body, // DEPRECATED
        .g-card-body {
            @include flex-row-stretch-top;

            @include media-breakpoint-up(md) {
                @include flex-row-stretch-center;
            }

            .g-card-alert-icon {
                width: rem(48);
                height: rem(48);
                margin-top: 0;
                font-size: rem(48);
                line-height: rem(48);
            }
        }
    }
}

// --- buttons inside alerts ---------------------------------------------------

// see button.scss !

// --- spacings to other elements ----------------------------------------------

%alert + .form-group {
    padding-top: $spacing-vertical-half;
}

// --- input alerts need a bit of tweaking of their spacings -------------------

%alert.g-card-alert-input,
input + %alert,
.input-group + %alert {
    margin-top: rem(5) !important; // stylelint-disable-line declaration-no-important
    margin-bottom: 0 !important; // stylelint-disable-line declaration-no-important
    border-top-width: rem(1);
    border-top-style: solid;

    .card-body, // DEPRECATED
    .g-card-body {
        padding-top: 0;
        padding-bottom: 0;
        padding-left: rem(10) !important; // stylelint-disable-line declaration-no-important

        .g-card-alert-data {
            margin-left: rem(5) !important; // stylelint-disable-line declaration-no-important
        }
    }

    &.g-card-alert-flag {
        .g-card-alert-data {
            margin-left: rem(10) !important; // stylelint-disable-line declaration-no-important
        }
    }
}

// --- small alerts within form-groups -----------------------------------------

.form-group .g-card-alert-small {
    margin-top: rem(5) !important; // stylelint-disable-line declaration-no-important
    margin-bottom: 0 !important; // stylelint-disable-line declaration-no-important
}

// --- closeable alerts adjustments --------------------------------------------

.g-card-alert-closeable {
    .card-body, // DEPRECATED
    .g-card-body {
        align-items: start !important; // stylelint-disable-line declaration-no-important
    }

    div[class*='col-md-8'] {
        justify-content: start !important; // stylelint-disable-line declaration-no-important
    }
}
