$spacing-horizontal-small: rem(70);
$spacing-horizontal-medium: rem(140);
$spacing-horizontal-large: rem(210);

$spacing-vertical-minimal: rem(10); // the smallest pixel-perfect spacing possible

$spacing-vertical: rem(20); // typical spacing between elements (e.g. paragraphs, boxes, ...)
$spacing-vertical-half: $spacing-vertical * 0.5;

$card-padding-horizontal: rem(15);
$card-padding-vertical: rem(20);
