@mixin fontstack {
    font-family: $theme-font-body;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
}

@mixin typo($tag, $lineHeight: true) {
    font-size: map-get($typo-font-size, $tag);
    @if $lineHeight {
        line-height: map-get($typo-line-height, $tag);
    }
}

@mixin typo-offset($tag, $reset: false, $top: true, $bottom: true, $first: true) {
    @if $reset {
        margin: 0;
        padding: 0;
    }
    @if $top {
        @if $first {
            padding-top: $spacing-vertical-half - map-get($typo-offset-top, $tag);
        } @else {
            padding-top: $typo-h-space-before -
                $spacing-vertical-half -
                map-get($typo-offset-top, $tag);
        }
    }
    @if $bottom {
        padding-bottom: $spacing-vertical-half - map-get($typo-offset-bottom, $tag);
    }
}

// deprecated mixins

@mixin typo-h1 {
    @include typo(h1);
}

@mixin typo-h2 {
    @include typo(h2);
}

@mixin typo-h3 {
    @include typo(h3);
}

@mixin typo-h4 {
    @include typo(h4);
}

@mixin typo-h5 {
    @include typo(h5);
}

@mixin typo-h6 {
    @include typo(h6);
}

@mixin typo-p-regular {
    @include typo(p);
}

@mixin typo-small {
    @include typo(small);
}

@mixin typo-p-major {
    @include typo(p-major);
}

@mixin typo-p-minor {
    @include typo(p-minor);
}

@mixin typo-p-micro {
    @include typo(p-micro);
}

@mixin typo-h-huge {
    @include typo(h-huge);
}

@mixin typo-tabular-numbers {
    font-variant-numeric: lining-nums tabular-nums;
}
