// George input fields - various kinds

// --- placeholders ------------------------------------------------------------

::placeholder {
    color: var(--g-color-text-secondary);
    font-size: inherit;
    line-height: inherit;
}

:disabled::placeholder {
    color: var(--g-color-text-disabled);
}

// --- standard input field types ----------------------------------------------

%form-control {
    @include form-control;
}

%input {
    @include input;
}

%input-hover {
    @include input-hover;
}

%input-focus {
    @include input-focus;
}

input {
    &[type='date'],
    &[type='datetime-local'],
    &[type='email'],
    &[type='month'],
    &[type='number'],
    &[type='password'],
    &[type='search'],
    &[type='tel'],
    &[type='text'],
    &[type='time'],
    &[type='url'],
    &[type='week'] {
        @extend %input;
    }

    &[type='checkbox'],
    &[type='radio'] {
        ~ .custom-control-label {
            font-weight: $typo-x-font-weight-regular;
        }
    }

    &[type='number'] {
        appearance: textfield;

        &::-webkit-outer-spin-button,
        &::-webkit-inner-spin-button {
            appearance: none;
        }
    }
}

textarea {
    &,
    &[readonly] {
        @extend %input;

        min-height: $input-control-size;
    }
}

// --- checkboxes and radio buttons --------------------------------------------

$g-custom-control-line-height: 1.5;
$g-custom-control-vertical-padding: rem(4);
$g-custom-control-indicator-size: rem(16);
$g-custom-control-indicator-icon-alignment: sass-div(
    $g-custom-control-line-height - $g-custom-control-indicator-size,
    2
);

.custom-control {
    display: block;
    position: relative;
    min-height: $g-custom-control-indicator-size * $g-custom-control-line-height;
    margin-right: 0;
    padding-left: rem(24);
    cursor: default;

    &:not(.custom-control-inline) {
        padding-top: $g-custom-control-vertical-padding;
        padding-bottom: $g-custom-control-vertical-padding;

        .custom-control-label {
            &::before,
            &::after {
                top: (
                    $g-custom-control-indicator-icon-alignment + $g-custom-control-vertical-padding
                );
            }
        }
    }
}

.custom-control-inline {
    display: inline-flex;
    margin-right: rem(16);
}

.custom-control-input {
    position: absolute;
    z-index: 10;
    top: 0;
    left: -($g-custom-control-indicator-size * 0.5);
    width: $g-custom-control-indicator-size * 2;
    height: $g-custom-control-indicator-size * 2;
    opacity: 0;

    &:checked {
        & ~ .custom-control-label {
            &::before {
                background-color: var(--g-color-interactive-primary);
                color: var(--g-color-interactive-primary);
            }
        }
    }

    &:not(:disabled) {
        &:not([readonly]) {
            &:hover ~ .custom-control-label {
                &::before {
                    color: var(--g-color-interactive-primary);
                }
            }

            &:active ~ .custom-control-label {
                &::before {
                    @include color-layer(rgba(0, 0, 0, 0.25));
                }
            }
        }

        &:focus-visible ~ .custom-control-label {
            &::before {
                @include element-focus-ring;
            }
        }
    }

    &:disabled {
        cursor: not-allowed;

        ~ .custom-control-label {
            color: var(
                --g-color-text-disabled
            ) !important; // stylelint-disable-line declaration-no-important

            cursor: not-allowed;

            &::before {
                background-color: var(
                    --g-color-surface-disabled
                ) !important; // stylelint-disable-line declaration-no-important

                background-image: none;
                box-shadow: none;
                color: var(
                    --g-color-border-disabled
                ) !important; // stylelint-disable-line declaration-no-important
            }
        }
    }
}

.custom-control-label {
    margin-bottom: 0;

    &::before,
    &::after {
        content: '';
        display: block;
        position: absolute;
        top: $g-custom-control-indicator-icon-alignment;
        left: 0;
        width: $g-custom-control-indicator-size;
        height: $g-custom-control-indicator-size;
        border: rem(1) solid transparent;
        background-clip: content-box;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain;
    }

    &::before {
        transition: $input-transition-property $input-transition-duration;
        border-color: currentColor;
        background-color: var(--g-color-surface-primary);
        background-image: $input-fake-shadow;
        color: var(--g-color-border-primary);
    }
}

.custom-checkbox {
    .custom-control-label {
        &::before {
            border-radius: rem(2);
        }
    }

    .custom-control-input {
        &:checked {
            ~ .custom-control-label {
                &::after {
                    background-color: var(--g-color-fill-inverted);
                    mask-image: svg-ico-checkbox();
                }
            }

            &:disabled ~ .custom-control-label {
                &::after {
                    background-color: var(--g-color-border-disabled);
                    mask-image: svg-ico-checkbox();
                }
            }
        }

        &:indeterminate {
            ~ .custom-control-label {
                &::before {
                    background-color: var(--g-color-interactive-primary);
                    color: var(--g-color-interactive-primary);
                }

                &::after {
                    background-color: var(--g-color-fill-inverted);
                    mask-image: svg-ico-minus();
                }
            }

            &:disabled ~ .custom-control-label {
                &::after {
                    background-color: var(--g-color-border-disabled);
                    mask-image: svg-ico-minus();
                }
            }
        }
    }
}

.custom-radio {
    .custom-control-label {
        &::before {
            border-radius: 50%;
            background-image: $input-fake-shadow-rounded;
        }
    }

    .custom-control-input {
        &:checked {
            ~ .custom-control-label {
                &::after {
                    background-color: var(--g-color-fill-inverted);
                    mask-image: svg-ico-circle();
                }
            }

            &:disabled ~ .custom-control-label {
                &::after {
                    background-color: var(--g-color-border-disabled);
                    mask-image: svg-ico-circle();
                }
            }
        }
    }
}

// --- selects -----------------------------------------------------------------

select {
    @extend %input;
}

%select-focus {
    z-index: 5;
    outline: none;
    text-decoration: none;
    @include element-focus-ring(true);
}

%select {
    @include ellipsis;
    @include typo(p);

    display: inline-flex;
    align-items: baseline;
    justify-content: flex-start;
    padding-right: rem(26);
    transition: $input-transition-property $input-transition-duration;
    border-color: var(--g-color-border-primary);
    outline: none;
    background-color: var(--g-color-surface-primary);
    background-image: none;
    color: var(--g-color-text-primary);
    font-weight: $typo-x-font-weight-regular;
    text-align: left;
    appearance: none;

    &:not(:disabled) {
        &.placeholder {
            color: var(--g-color-text-secondary);
        }

        &:hover {
            z-index: 5;
            border-color: var(--g-color-border-hover);
            outline: none;
            text-decoration: none;
        }

        &:focus {
            @extend %select-focus;
        }
    }

    &:disabled {
        @include input-disabled;

        &.placeholder {
            color: var(
                --g-color-text-disabled
            ) !important; // stylelint-disable-line declaration-no-important
        }
    }
}

%select-arrow {
    @include use-caret;

    &::after {
        position: absolute;
        top: rem(12);
        right: rem(8);
    }
}

.g-form-select {
    @extend %select-arrow;

    position: relative;

    select {
        @extend %select;

        height: $input-control-size !important; // stylelint-disable-line declaration-no-important

        &.custom-select {
            padding-left: rem(10 - 3); // subtract inner padding from <option>
        }
    }

    option {
        color: $input-color;

        &:disabled {
            color: var(--g-color-text-disabled);
        }
    }

    &.disabled {
        cursor: not-allowed;
    }
}

// --- advanced ----------------------------------------------------------------

.form-control {
    @extend %form-control;
}

.row + .form-group {
    margin-top: rem(16);

    @include media-breakpoint-down(sm) {
        margin-top: rem(8);
    }
}

.card-body, // DEPRECATED
.g-card-body {
    .form-group:first-child {
        @include media-breakpoint-up(md) {
            margin-top: $input-control-shift;
        }
    }
}

.form-group {
    margin-bottom: $input-control-shift;

    &.g-form-compact {
        margin-top: 0;
        margin-bottom: 0;
    }

    .col-form-label {
        @include typo(p);

        margin-bottom: ($input-control-shift + $input-border-width) * -1;
        padding-top: $spacing-vertical-half - $typo-p-offset-top + rem(2); // move 2px down to match input fields to the right
        padding-bottom: $spacing-vertical-half - $typo-p-offset-bottom + $input-control-shift;
        color: var(--g-color-text-primary);
        font-weight: $typo-x-font-weight-semi-bold;
        hyphens: auto;
    }

    .g-form-description {
        margin-bottom: $input-control-shift * -1;

        &,
        > p {
            @include typo(small);
        }
    }

    .g-card-alert-small {
        > .card-body, // DEPRECATED
        > .g-card-body {
            padding-top: 0;
            padding-bottom: 0;
        }
    }

    @mixin form-input-col($tier: all) {
        &:not(:first-child) {
            > :first-child {
                &:not(
                        .row,
                        .input-group,
                        .form-control,
                        .form-pseudo-control,
                        .dropdown,
                        .dropdown-toggle
                    ) {
                    @if $tier == all {
                        &:not(label, legend) {
                            // move stuff in second column down 2px if not an input field
                            // so baseline matches the label
                            margin-top: rem(2);
                        }
                    } @else {
                        // move stuff up in second column in responsive mode so the
                        // content is closer to the label
                        margin-top: $input-label-content-responsive-gap -
                            $input-form-spacing -
                            ($spacing-vertical-half - $typo-p-offset-top + rem(2));
                    }

                    @if $tier == sm {
                        // stylelint-disable-next-line declaration-no-important
                        margin-top: $input-control-shift * -1 !important;
                    }
                }
            }
        }
    }

    [class^='col'] {
        @include form-input-col(all);

        .form-group {
            margin-bottom: 0;
        }
    }

    // TODO: check if stuff below is really necessary, because it's probably not

    .col,
    .col-12 {
        @include form-input-col(xxl);
    }

    @include media-breakpoint-down(xl) {
        .col,
        [class^='col-xxl'] {
            @include form-input-col(xl);
        }
    }

    @include media-breakpoint-down(lg) {
        .col,
        [class^='col-xl'],
        [class^='col-xxl'] {
            @include form-input-col(lg);
        }
    }

    @include media-breakpoint-down(md) {
        .col,
        [class^='col-lg'],
        [class^='col-xl'],
        [class^='col-xxl'] {
            @include form-input-col(md);
        }
    }

    @include media-breakpoint-down(sm) {
        .col,
        .col-12,
        [class^='col-md'],
        [class^='col-lg'],
        [class^='col-xl'],
        [class^='col-xxl'] {
            @include form-input-col(sm);
        }
    }

    @include media-breakpoint-down(xs) {
        .col,
        [class^='col-sm'],
        [class^='col-md'],
        [class^='col-lg'],
        [class^='col-xl'],
        [class^='col-xxl'] {
            @include form-input-col(xs);
        }
    }
}

.g-form-multicol {
    .form-group {
        // add a space between all 'rows'
        margin-top: rem(8);
    }

    &:first-of-type {
        // remove it from the first row and thus the first item in 1- or 2-col mode
        margin-top: rem(-8);
    }

    label,
    legend {
        @include ellipsis;
    }
}

.input-group {
    flex-wrap: nowrap;

    > .input-group-prepend,
    > .input-group-append {
        background-color: var(--g-color-surface-primary);

        > .input-group-text {
            min-height: $input-control-size;
            padding: 0 rem(9);
            border-color: var(--g-color-border-primary);
            background-color: var(--g-color-surface-primary);
            color: var(--g-color-text-primary);
            font-weight: $typo-x-font-weight-regular;

            svg {
                // stylelint-disable-next-line scss/at-extend-no-missing-placeholder
                @extend .g-image-24;
            }
        }

        > .btn {
            // overrule button settings so it always looks input-field like
            min-height: $input-control-size;
            padding: 0 rem(8);
            border-color: var(
                --g-color-border-primary
            ) !important; // stylelint-disable-line declaration-no-important

            background-color: var(--g-color-surface-primary);
            background-image: unset;
            color: var(--g-color-interactive-primary);
            font-size: map-get($typo-font-size, p);

            > svg {
                // stylelint-disable-next-line scss/at-extend-no-missing-placeholder
                @extend .g-image-24;

                margin-right: 0;
                margin-left: 0;
            }
        }
    }

    &.disabled {
        .input-group-prepend,
        .input-group-append {
            > .input-group-text,
            > .btn,
            > .g-button {
                @include input-disabled;
            }
        }
    }
}

// --- spacing to/from other elements ------------------------------------------

input,
select,
.dropdown {
    p,
    ul,
    ol {
        margin-top: rem(16) - $typo-p-offset-top;
    }
}
