// this file is all about buttons

@include btn-variants('primary', $color-white, $color-blue-300, $solid: true);
@include btn-variants('secondary', $color-blue-300, $color-gray-300);
@include btn-variants('danger', $color-ui-red, rgba($color-ui-red, 0.5), $advanced: false);
@include btn-variants('transparent', $color-white, rgba($color-white, 0.5)); // DEPRECATED

.g-btn-ui-white {
    // DEPRECATED
    @extend %btn-white !optional;
    @include btn-style-ui-white($color-white, rgba($color-white, 0.5));
}

// special buttons for alert boxes

@include btn-variants('alert-error', $color-blue-300, $color-gray-300); // DEPRECATED
@include btn-variants('alert-warning', $color-blue-300, $color-gray-300); // DEPRECATED
@include btn-variants('alert-info', $color-blue-300, $color-gray-300); // DEPRECATED

// bootstrap tweaks
%btn,
.btn {
    @include btn;

    &,
    > span:not(.sr-only) {
        @include ellipsis;
    }

    > span:not(.sr-only),
    > svg {
        // fix for ie displacing button contents on click
        position: relative;
    }

    > .g-spinner {
        --size: #{rem(24)};
    }

    &.g-button-loading {
        position: relative;
        justify-content: center !important; // stylelint-disable-line declaration-no-important

        > .g-spinner {
            position: absolute;
            margin: auto;

            ~ span,
            ~ svg {
                visibility: hidden;
            }
        }
    }
}

// --- size variants -----------------------------------------------------------

.btn-lg {
    min-height: rem(46);

    > .g-spinner {
        --size: #{rem(24)};
    }
}

.btn-sm {
    min-height: rem(26);
    padding: 0 ($button-padding-horizontal * 0.5);
    font-size: rem(14);

    // TODO remove in GDS v3
    > span:not(.sr-only) {
        top: 0;
    }

    > .g-spinner {
        --size: #{rem(16)};
    }
}

.g-btn-ui {
    margin-right: -($button-padding-horizontal * 0.5 + $button-border-size);
    margin-left: -($button-padding-horizontal * 0.5 + $button-border-size);
    line-height: rem(16); // hack for small UIButton span vertical alignment

    &.g-btn-icon-only {
        margin-right: -($button-padding-horizontal * 0.5 + $button-border-size);
        margin-left: -($button-padding-horizontal * 0.5 + $button-border-size);

        &.btn-sm {
            margin-right: -($button-padding-horizontal * 0.25 + $button-border-size);
            margin-left: -($button-padding-horizontal * 0.25 + $button-border-size);
        }
    }
}

.g-inverted {
    .g-btn-ui {
        color: $typo-color-text-inverted;

        &:active,
        &:focus {
            color: $typo-color-text-inverted;
        }

        &:active {
            color: $typo-color-text-inverted;
        }

        &:disabled {
            color: rgba($typo-color-text-inverted, 0.5);
        }
    }
}

// --- icon buttons ------------------------------------------------------------

%btn-icon {
    padding-right: rem(8);
    padding-left: rem(8);

    > svg {
        // stylelint-disable-next-line scss/at-extend-no-missing-placeholder
        @extend .g-image-24; // default icons are 24x24

        ~ span:not(.sr-only) {
            margin-left: rem(5);
        }
    }

    &.btn-sm {
        padding-right: rem(8);
        padding-left: rem(4);

        > svg {
            // stylelint-disable-next-line scss/at-extend-no-missing-placeholder
            @extend .g-image-16; // smaller icons are 16x16
        }
    }

    &.g-btn-ui {
        justify-content: flex-end;
        padding-right: rem(4);
        padding-left: rem(4);

        > svg {
            order: 1;
        }

        &.g-btn-ui-toggleable {
            > svg {
                transition: transform 300ms ease-in-out;
                will-change: transform;
            }

            &[aria-expanded='true'] {
                > svg {
                    transform: rotate(180deg);
                }
            }
        }

        > span:not(.sr-only) {
            margin-right: rem(5);
            margin-left: 0;
        }
    }
}

.g-btn-icon {
    @extend %btn-icon;
}

.g-btn-icon-only {
    @extend %btn-icon;

    padding: 0 $button-spacing-horizontal * 0.5 - $button-border-size;

    &.btn-sm {
        padding: 0 $button-spacing-horizontal * 0.25 - $button-border-size;
    }

    &.btn-lg {
        padding: 0 sass-div($button-spacing-horizontal, 1.5) - $button-border-size;
    }

    + .g-btn-icon-only {
        margin-left: $button-spacing-horizontal * 0.5;
    }

    > span {
        @include sr-only;
    }
}

// --- button spacing ----------------------------------------------------------

.g-group-button {
    margin: 0 (-$button-spacing-horizontal * 0.5);

    &.g-group {
        // remove spacings from a button group as the margin is attached to the buttons inside instead.
        margin-top: 0;
        margin-bottom: 0;
        padding-top: 0;
        padding-bottom: 0;
    }

    &.g-group-button-form {
        margin-top: $button-spacing-horizontal;
        text-align: right;
    }

    .btn {
        margin: ($button-spacing-vertical * 0.5) ($button-spacing-horizontal * 0.5);

        &.g-btn-icon-only {
            &:not(:first-of-type) {
                margin-left: 0;
            }
        }
    }

    &.g-group-button-segmented {
        display: flex;
        width: auto;

        &-inline {
            @include media-breakpoint-up(sm) {
                display: inline-flex;
                width: auto;
                max-width: 100%;
            }
        }

        .btn {
            &:not(.g-btn-icon-only) {
                flex-grow: 1;
            }

            &:not(.g-btn-ui) {
                &:not(:last-child) {
                    @include media-breakpoint-up(sm) {
                        margin-right: 0;
                        border-top-right-radius: 0;
                        border-bottom-right-radius: 0;
                    }

                    @include media-breakpoint-down(xs) {
                        margin-bottom: 0;
                        border-bottom-right-radius: 0;
                        border-bottom-left-radius: 0;
                    }
                }

                &:not(:first-child) {
                    @include media-breakpoint-up(sm) {
                        margin-left: -$button-border-size;
                        border-top-left-radius: 0;
                        border-bottom-left-radius: 0;
                    }

                    @include media-breakpoint-down(xs) {
                        margin-top: -$button-border-size;
                        border-top-left-radius: 0;
                        border-top-right-radius: 0;
                    }
                }
            }
        }
    }
}

// --- responsive variants -----------------------------------------------------

@include media-breakpoint-down(sm) {
    .g-btn-ui {
        > span {
            @include sr-only;
        }
    }
}

@include media-breakpoint-down(xs) {
    :not(.input-group-append) > .btn {
        &:not(.g-btn-ui, .g-btn-icon-only) {
            width: 100%;
            margin-right: 0;
            margin-left: 0;
        }

        &.g-btn-icon-only {
            margin-left: 0;

            & + .g-btn-icon-only {
                margin-left: $button-spacing-vertical * 0.5;
            }
        }
    }

    .g-group-button {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        margin-right: 0;
        margin-left: 0;

        .g-btn-primary {
            order: -1;
        }
    }
}
