@mixin increase-specificity($depth: 1) {
    $sel: '';

    @while $depth > 0 {
        $sel: $sel + ':nth-child(n)';
        $depth: $depth - 1;
    }

    @if & {
        &#{$sel} {
            @content;
        }
    } @else {
        #{$sel} {
            @content;
        }
    }
}

@mixin at-namespace($selector, $namespace: '.g-bootstrap') {
    $original: #{&};
    $modified: selector-replace($original, $namespace, '#{$namespace}#{$selector}');

    @if is-superselector($modified, $original) {
        :global(#{$selector}) & {
            @content;
        }
    } @else {
        @at-root {
            #{$modified} {
                @content;
            }
        }
    }
}

// DEPRECATED
@mixin uses-keyboard {
    @include at-namespace('.g-uses-keyboard') {
        @content;
    }
}

@mixin sr-only {
    // stylelint-disable declaration-no-important
    position: absolute !important;
    top: auto !important;
    left: auto !important;
    width: 1px !important;
    height: 1px !important;
    margin: -1px !important;
    padding: 0 !important;
    overflow: hidden !important;
    clip: rect(1px, 1px, 1px, 1px) !important;
    border: 0 !important;
    white-space: nowrap !important;
    clip-path: inset(50%) !important;
    // stylelint-enable declaration-no-important
}

@mixin sr-only-soft {
    position: absolute;
    top: auto;
    left: auto;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(1px, 1px, 1px, 1px);
    border: 0;
    white-space: nowrap;
    clip-path: inset(50%);
}

@mixin sr-only-soft-unset {
    position: unset;
    top: unset;
    left: unset;
    width: unset;
    height: unset;
    overflow: unset;
    clip: unset;
    border: unset;
    white-space: unset;
    clip-path: unset;
}

@mixin hide-scrollbar {
    // There used to be a CSS rule that could hide scrollbars in Firefox, but it has since been deprecated.
    scrollbar-width: none;

    // Hide scrollbars in Webkit-based browsers (Chrome and Safari).
    &::-webkit-scrollbar {
        display: none;
    }
}

@mixin element-focus-ring($subtle: false) {
    box-shadow: 0 0 0 #{if($subtle, $element-focus-size-subtle, $element-focus-size)} $element-focus-color;
}

@mixin element-focus($suffix: '', $keyboardOnly: true, $subtle: false) {
    @if $keyboardOnly {
        @include uses-keyboard {
            &:focus#{$suffix} {
                @include element-focus-ring($subtle);
            }
        }
    } @else {
        &:focus#{$suffix} {
            @include element-focus-ring($subtle);
        }
    }
}

@mixin use-caret {
    &::after {
        content: '';
        z-index: z-index-level(base, 1); // so it works in input-addons, too.
        width: rem(16);
        height: rem(16);
        margin: 0;
        transition: transform 300ms ease-in-out;
        border: 0 none;
        background: var(--color, $color-blue-300);
        cursor: pointer;
        pointer-events: none; // fix non clickable triangle
        mask: svg-ico-caret-down($color-white) center right no-repeat;
        will-change: transform;
    }

    &.disabled,
    &:disabled {
        &::after {
            background: var(--color, $input-disabled-color);
        }
    }

    &.show,
    &[aria-expanded='true'] {
        &::after {
            transform: rotate(180deg);
        }
    }
}

@mixin cover-parent($offset: 0, $z-index: z-index-level(base, 0)) {
    position: absolute;
    z-index: $z-index;
    inset: $offset;
}

@mixin cover-parent-exclude($z-index: z-index-level(base, 1)) {
    position: relative;
    z-index: $z-index;
}
