@mixin btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    min-height: rem(36);
    padding: 0 $button-padding-horizontal;
    transition-property: color, background-color, border, box-shadow;
    transition-duration: 100ms;
    transition-timing-function: ease-out;
    border-radius: rem(4);
    font-family: inherit;
    font-size: rem(16);
    font-weight: $typo-x-font-weight-regular;

    &:not(:disabled) {
        // we want pointers on all buttons
        cursor: pointer;
    }
}

@mixin btn-blend {
    margin: 0;
    padding: 0;
    border: 0;
    outline: none !important; // stylelint-disable-line declaration-no-important
    color: inherit;
    font-family: inherit;
    font-size: 100%;
    line-height: inherit;
    appearance: none;
}

@mixin btn-color($color-fg, $color-bg: $color-transparent, $color-outline: $color-transparent) {
    border: $button-border-size solid $color-outline;
    background-color: $color-bg;
    color: $color-fg;
}

@mixin btn-group-neighbouring {
    // mixin to be used when reduced vertical button spacing is allowed
    margin-top: $button-spacing-vertical-reduced * 0.5;
    margin-bottom: $button-spacing-vertical-reduced * 0.5;
}

@mixin btn-state-hover($color-layer, $color-shadow, $color-border: null) {
    &:hover {
        box-shadow: 0 rem(4) rem(12) rem(-4) $color-shadow;
        text-decoration: none; // needed when button is a link
        @include color-layer($color-layer);
        @if $color-border {
            border-color: $color-border;
        }
    }
}

@mixin btn-state-focus {
    &:focus {
        outline: none;
        box-shadow: none; // override for Bootstrap focus styles
    }

    &:focus-visible {
        border-color: $color-blue-200;
        outline: none;
        box-shadow: 0 0 0 #{rem(4) - $button-border-size} $color-blue-200;
    }
}

// DEPRECATED (not needed in GDS, but george-web:)
@mixin btn-custom-focus($color-glow: null) {
    @include uses-keyboard {
        outline: none;
        @if $color-glow {
            box-shadow: 0 0 rem(4) rem(1) rgba($color-glow, 0.45);
        } @else {
            box-shadow: none;
        }
    }
}

@mixin btn-state-active($color-text, $color-layer, $color-shadow, $color-border: null) {
    &:active {
        box-shadow: 0 rem(4) rem(4) rem(-4) $color-shadow;
        color: $color-text; // needed when button is a link
        @include color-layer($color-layer);
        @if $color-border {
            border-color: $color-border;
        }
    }
}

@mixin btn-style-solid($color-text, $color-complement) {
    @include btn-color($color-text, $color-complement);

    &:not(:disabled) {
        @include btn-state-hover(rgba($color-black, 0.15), rgba($color-complement, 0.6));
        @include btn-state-focus;
        @include btn-state-active($color-text, rgba($color-black, 0.2), $color-complement);
    }

    &:disabled {
        @include btn-color(rgba($color-black, 0.25), $button-color-disabled);
    }
}

@mixin btn-style-outline($color-text, $color-complement) {
    @include btn-color($color-text, $color-transparent, $color-complement);

    &:not(:disabled) {
        $color-outline: rgba($color-complement, 0.6);
        @include btn-state-hover(rgba($color-text, 0.06), $color-outline, $color-outline);
        @include btn-state-focus;
        @include btn-state-active(
            $color-text,
            rgba($color-text, 0.1),
            $color-complement,
            rgba($color-complement, 0.7)
        );
    }

    &:disabled {
        @include btn-color($button-color-disabled, $color-transparent, $button-color-disabled);
    }
}

@mixin btn-style-outline-color($color-text, $color-complement) {
    @include btn-color($color-text, $color-transparent, $color-complement);

    &:not(:disabled) {
        @include btn-state-hover(
            rgba($color-black, 0.1),
            rgba($color-black, 0.2),
            rgba($color-complement, 0.7)
        );
        @include btn-state-focus;
        @include btn-state-active(
            $color-text,
            rgba($color-black, 0.15),
            rgba($color-black, 0.3),
            rgba($color-complement, 0.7)
        );
    }

    &:disabled {
        opacity: 0.3 !important; // stylelint-disable-line declaration-no-important
    }
}

@mixin btn-style-link($color-text, $color-complement) {
    @include btn-color($color-text, $color-transparent, $color-transparent);

    &:not(:disabled) {
        $color-outline: rgba($color-complement, 0.6);
        @include btn-state-hover(rgba($color-text, 0.06), $color-outline, $color-outline);
        @include btn-state-focus;
        @include btn-state-active(
            $color-text,
            rgba($color-text, 0.1),
            $color-complement,
            rgba($color-complement, 0.7)
        ); // stylelint-disable-line
    }

    &:disabled {
        @include btn-color($button-color-disabled);
    }
}

@mixin btn-style-ui-white($color-text, $color-complement) {
    @include btn-color($color-text, $color-transparent, $color-transparent);

    &:not(:disabled) {
        $color-outline: rgba($color-complement, 0.6);
        @include btn-state-hover(
            rgba($color-black, 0.1),
            rgba($color-black, 0.2),
            rgba($color-complement, 0.7)
        );
        @include btn-state-focus;
        @include btn-state-active(
            $color-text,
            rgba($color-text, 0.1),
            $color-complement,
            rgba($color-complement, 0.7)
        ); // stylelint-disable-line
    }

    &:disabled {
        @include btn-color($button-color-disabled);
    }
}

@mixin btn-variants($type, $color-text, $color-complement, $solid: false, $advanced: true) {
    // DEPRECATED
    // In the transition period between GDS v2 and v3 we generate more-than-ever button classNames.
    // Both used and unused will be removed after the deprecation period of the old buttons.

    @if $solid {
        .g-btn-#{$type} {
            @extend %btn-#{$type} !optional;
            @include btn-style-solid($color-text, $color-complement);
        }

        // retrospective hack to make sure old buttons still work
        .g-btn-icon-#{$type} {
            @extend %btn-#{$type} !optional;

            background-color: $color-transparent;
            color: $color-complement;

            &:hover {
                @include btn-style-outline($color-text, $color-complement);

                color: $color-complement;
            }

            &:active {
                @include btn-style-outline($color-complement, $color-complement);
            }
        }
    } @else {
        .g-btn-outline-#{$type} {
            @extend %btn-#{$type} !optional;
            @include btn-style-outline($color-text, $color-complement);
            @if $type == 'secondary' {
                &.active {
                    @include btn-color($color-white, $color-complement);

                    &:disabled {
                        @include btn-color(
                            rgba($color-white, 0.5),
                            $button-color-disabled,
                            $button-color-disabled
                        );
                    }
                }
            }
            @if $type == 'transparent' {
                @include btn-style-outline-color($color-white, $color-complement);
            }
        }

        // retrospective hack to make sure old buttons still work
        .g-btn-icon-#{$type} {
            @extend %btn-#{$type} !optional;
            @include btn-state-focus;

            background-color: $color-transparent;
            color: $color-text;

            &:hover {
                @include btn-style-outline($color-text, $color-complement);
            }
        }
    }

    @if $advanced {
        .g-btn-icon-#{$type} {
            @extend %g-btn-icon !optional;
            @include btn-state-focus;
        }
        @if $type != 'transparent' {
            .g-btn-link-#{$type},
            .g-btn-ui-#{$type} {
                @extend %btn-#{$type} !optional;
                @include btn-style-link($color-blue-300, $color-gray-300);
                @include btn-state-focus;
            }
        }
    }
}
