// the huge cards used in store detail pages steps 1..3

// width: 940

$checkout-color-headline1: $color-blue-300;
$checkout-color-headline2: $color-blue-400;
$checkout-color-text-faded: $color-gray-300;
$checkout-color-background-grey: $color-gray-100;
$checkout-color-separator: $color-separator;

$checkout-h1-offset-top: rem(10);
$checkout-h1-offset-bottom: rem(6);
$checkout-h2-offset-top: rem(6);
$checkout-h2-offset-bottom: rem(5);

$checkout-spacing: rem(20);
$checkout-spacing-h1h2: $checkout-spacing;
$checkout-spacing-large: rem(40);

$checkout-spacing-horizontal: rem(30);

$checkout-summary-spacing: rem(30);

$checkout-checkbox-indent: rem(20);

$checkout-header-spacing: rem(40);
$checkout-body-spacing: rem(40);
$checkout-footer-spacing: rem(30);

.g-card.g-card-checkout {
    border-color: $checkout-color-separator !important; // stylelint-disable-line declaration-no-important

    --card-padding-horizontal: #{$checkout-spacing-horizontal};

    @include media-breakpoint-up(lg) {
        --card-padding-horizontal: #{rem(140)};

        &.g-card-checkout-small {
            --card-padding-horizontal: #{$spacing-horizontal-medium};
        }
    }

    p,
    li {
        font-weight: $typo-x-font-weight-regular;

        strong,
        b {
            font-weight: $typo-x-font-weight-semi-bold;

            &.g-amount {
                color: $color-blue-300;
            }
        }
    }

    .g-faded {
        color: $checkout-color-text-faded;
    }

    a {
        font-weight: $typo-x-font-weight-semi-bold;
    }

    $checkout-h1-offset-top: rem(10);
    $checkout-h1-offset-bottom: rem(7);
    $checkout-h2-offset-top: rem(6);
    $checkout-h2-offset-bottom: rem(5);
    $checkout-h3-offset-top: rem(4);
    $checkout-h3-offset-bottom: rem(5);

    &.g-card-checkout-header {
        --card-padding-vertical: #{$checkout-header-spacing - $spacing-vertical-half};

        .card-body, // DEPRECATED
        .g-card-body {
            text-align: center;
        }
    }

    &.g-card-checkout-body {
        --card-padding-vertical: #{$checkout-body-spacing - $spacing-vertical-half};

        > .card-body, // DEPRECATED
        > .g-card-body {
            .row > [class^='col']:first-child {
                // images in the first colum get a maximum size and are centered
                img,
                svg {
                    display: block;
                    max-width: 100%;
                    max-height: rem(168);
                    margin-right: auto;
                    margin-left: auto;

                    @include media-breakpoint-down(sm) {
                        max-width: rem(210);
                    }
                }
            }

            > .form-group:first-child {
                margin-top: 0;
            }
        }
    }

    // confirm pages a.k.a. "success screens"
    &.g-card-checkout-confirm {
        .card-body, // DEPRECATED
        .g-card-body {
            padding-top: rem(120);
            padding-bottom: rem(40);
            text-align: center;

            %heading {
                @include typo(h2);

                padding-top: $spacing-vertical-half - $typo-h2-offset-top;
                padding-bottom: $spacing-vertical-half - $typo-h2-offset-bottom;
                color: $checkout-color-headline2;
                font-weight: $typo-h-font-weight;
                text-align: center;
            }

            svg + %heading-tags {
                // do not add a VisuallyHidden label to the icon, otherwise the plus selector will fail
                padding-top: $spacing-vertical-half - $typo-h2-offset-top + rem(20);
            }
        }
    }

    &.g-card-checkout-footer {
        .card-body, // DEPRECATED
        .g-card-body {
            padding-top: $checkout-footer-spacing - $spacing-vertical-half;
            padding-bottom: $checkout-footer-spacing - $spacing-vertical-half;
            text-align: center;
        }
    }
}

// rules for gap/nogap behavor of two succeeding body cards

.g-card-checkout-body:not(.g-card-checkout-gap) + .g-card-checkout-body {
    border-top: 0 !important; // stylelint-disable-line declaration-no-important

    .card-body, // DEPRECATED
    .g-card-body {
        padding-top: 0 !important; // stylelint-disable-line declaration-no-important
    }
}

.g-card-checkout-confirm + .g-card-checkout-footer {
    min-height: $product-footer-min-height;
    border-top: 0 !important; // stylelint-disable-line declaration-no-important
}
