// Inter 400=regular, 600=semibold, 700=bold

$theme-font-body: 'Inter var', 'Inter', sans-serif;

// to compensate for line height(s) we use offsets so we can create pixel-perfect margins
// note: those have to be re-measured using screenshots when the line-height or fontface is changed!

$typo-x-font-weight-regular: 400; // tag-agnostic
$typo-x-font-weight-medium: 500; // tag-agnostic
$typo-x-font-weight-semi-bold: 600; // tag-agnostic
$typo-x-font-weight-bold: 700; // tag-agnostic
$typo-p-font-weight: $typo-x-font-weight-regular;
$typo-p-font-weight-regular: $typo-x-font-weight-regular;
$typo-p-font-weight-medium: $typo-x-font-weight-medium;
$typo-p-font-weight-semi-bold: $typo-x-font-weight-semi-bold;
$typo-p-font-weight-bold: $typo-x-font-weight-bold;
$typo-h-font-weight: $typo-x-font-weight-bold;
$typo-h-font-weight-regular: $typo-x-font-weight-regular;
$typo-h-font-weight-bold: $typo-x-font-weight-bold;

$typo-x-font-weight: $typo-x-font-weight-regular; // DEPRECATED

$typo-font-size: (
    h1: rem(28),
    h2: rem(22),
    h3: rem(18),
    h4: rem(18),
    h5: rem(16),
    h6: rem(16),
    p: rem(16),
    small: rem(14),
    p-major: rem(18),
    p-minor: rem(14),
    p-micro: rem(12),
    h-huge: rem(32),
);
$typo-line-height: (
    h1: rem(32),
    h2: rem(28),
    h3: rem(24),
    h4: rem(20),
    h5: rem(20),
    h6: rem(20),
    p: rem(24),
    small: rem(21),
    p-major: rem(24),
    p-minor: rem(21),
    p-micro: rem(18),
    h-huge: rem(38),
);
$typo-offset-top: (
    h1: rem(7),
    h2: rem(6),
    h3: rem(6),
    h4: rem(4),
    h5: rem(4),
    h6: rem(4),
    p: rem(6),
    small: rem(5),
    p-major: rem(4),
    p-minor: rem(4),
    p-micro: rem(4),
    h-huge: rem(9),
);
$typo-offset-bottom: (
    h1: rem(5),
    h2: rem(5),
    h3: rem(5),
    h4: rem(3),
    h5: rem(3),
    h6: rem(3),
    p: rem(6),
    small: rem(5),
    p-major: rem(4),
    p-minor: rem(6),
    p-micro: rem(5),
    h-huge: rem(6),
);

$typo-hr-spacer: rem(16); // DEPRECATED
$typo-h-space-before: rem(40); // default space before headlines

// font colors
$typo-color-text: $color-blue-400;
$typo-color-text-faded: $color-gray-400;
$typo-color-text-inverted: $color-white;
$typo-color-text-inverted-faded: rgba($typo-color-text-inverted, 0.75);
$typo-color-text-on-grey: $color-blue-400;
$typo-color-text-on-grey-faded: $color-gray-400; // $typo-color-text-on-grey-faded is about to become deprecated

$typo-list-indent: rem(24);
$typo-color-bullet: $typo-color-text;

$typo-color-separator-light: $color-separator; // same as -dark below
$typo-color-separator-dark: $color-gray-200;

// deprecated variables
$typo-h1-offset-top: map-get($typo-offset-top, h1);
$typo-h1-offset-bottom: map-get($typo-offset-bottom, h1);
$typo-h2-offset-top: map-get($typo-offset-top, h2);
$typo-h2-offset-bottom: map-get($typo-offset-bottom, h2);
$typo-h3-offset-top: map-get($typo-offset-top, h3);
$typo-h3-offset-bottom: map-get($typo-offset-bottom, h3);
$typo-h4-offset-top: map-get($typo-offset-top, h4);
$typo-h4-offset-bottom: map-get($typo-offset-bottom, h4);
$typo-h5-offset-top: map-get($typo-offset-top, h5);
$typo-h5-offset-bottom: map-get($typo-offset-bottom, h5);
$typo-h6-offset-top: map-get($typo-offset-top, h6);
$typo-h6-offset-bottom: map-get($typo-offset-bottom, h6);
$typo-p-offset-top: map-get($typo-offset-top, p);
$typo-p-offset-bottom: map-get($typo-offset-bottom, p);
$typo-small-offset-top: map-get($typo-offset-top, small);
$typo-small-offset-bottom: map-get($typo-offset-bottom, small);
$typo-p-major-offset-top: map-get($typo-offset-top, p-major);
$typo-p-major-offset-bottom: map-get($typo-offset-bottom, p-major);
$typo-p-minor-offset-top: map-get($typo-offset-top, p-minor);
$typo-p-minor-offset-bottom: map-get($typo-offset-bottom, p-minor);
$typo-p-micro-offset-top: map-get($typo-offset-top, p-micro);
$typo-p-micro-offset-bottom: map-get($typo-offset-bottom, p-micro);
$typo-huge-offset-top: map-get($typo-offset-top, h-huge);
$typo-huge-offset-bottom: map-get($typo-offset-bottom, h-huge);
