// stylelint-disable declaration-no-important

[class*='g-fg-'] {
    color: var(--color-fg) !important;
}

[class*='g-bg-'] {
    background-color: var(--color-bg) !important;
    color: var(--color-fg) !important;
}

@mixin color-class(
    $name,
    $color,
    $complement-lightMode: var(--color-blue-400),
    $complement-darkMode: var(--color-white),
    $rgb-overlay: $rgb-overlay-dark
) {
    .g-fg-#{$name} {
        --color-fg: #{$color};
    }

    .g-bg-#{$name} {
        --color-bg: #{$color};
        --color-fg: #{$complement-lightMode};
        --rgb-overlay: #{$rgb-overlay};
    }

    &.g-dark {
        .g-bg-#{$name} {
            --color-fg: #{$complement-darkMode};
        }
    }
}

