.g-meter {
    display: block;
    position: relative;
    width: 100%;
    height: var(--size);
    overflow: hidden;
    border-radius: calc(var(--size) / 2);
    background-color: rgba($color-gray-400, 0.1);
    font-weight: $typo-x-font-weight-semi-bold;
    line-height: var(--size);
    isolation: isolate;

    @include typo(p-micro, $lineHeight: false);

    &-small {
        --size: #{rem(2)};
    }

    &-medium {
        --size: #{rem(6)};
    }

    &-large {
        --size: #{rem(20)};

        border-radius: rem(4);
    }

    &::before,
    &::after {
        content: '';
        position: absolute;
        z-index: -1;
        top: 0;
    }

    &::before {
        left: 0;
        width: var(--aux-value, var(--value));
        border-bottom: var(--size) solid var(--color);
    }

    &::after {
        left: var(--aux-value, 0);
        width: calc(var(--value) - var(--aux-value, 0));
        border-bottom: var(--size) solid var(--aux-color);
    }

    @media (forced-colors: active) {
        outline: rem(1) dashed CanvasText;
        outline-offset: rem(-1);

        .g-meter-label {
            background-color: Canvas;
        }

        &::before {
            opacity: 0.8;
        }

        &::after {
            opacity: 0.4;
        }
    }

    .g-meter-label {
        margin-left: rem(4);
        padding: 0 rem(4);
        border-radius: rem(3);
    }

    &.g-meter-label-offset {
        padding-left: var(--value);
    }

    &:not(.g-meter-label-offset) {
        color: $color-white;
    }
}
