// ### spacing rules ###########################################################

// create a set of rules that make pixelperfect spacings between p/h*/etc
// that take into account:
// - paddings to the (card) border
// - bootstrap rows/columns
// does not take into account:
// - colors
// - font styles
// this is done by adding half of the necessary spacing as padding before and
// half after each element

p,
ul,
ol {
    @include typo(p);
    @include typo-offset(p, $reset: true);
}

dt,
dd {
    @include typo(p);
}

.g-small {
    // "small print", i.e. legal text
    @include typo(p-minor, $lineHeight: false);

    line-height: 1.5; // TODO: remove when GDSWEB-1416 is implemented

    p,
    ul,
    ol,
    dt,
    dd {
        font-size: inherit;
        line-height: inherit;
    }
}

.g-micro {
    @include typo(p-micro);
}

small {
    font-size: inherit;
}

@for $level from 1 through 6 {
    $tag: h#{$level};
    $offset-top: map-get($typo-offset-top, $tag);
    $offset-bottom: map-get($typo-offset-bottom, $tag);

    #{$tag} {
        @extend %heading-tags !optional;
        @extend %heading !optional;
    }

    .g-#{$tag} {
        @extend %heading-classes !optional;
        @extend %heading !optional;
    }

    #{$tag},
    .g-#{$tag} {
        @include typo($tag);
        @include typo-offset($tag, $reset: true);

        color: $color-blue-400;
        font-weight: $typo-h-font-weight-bold;

        &:not(.g-first) {
            @include typo-offset($tag, $first: false, $bottom: false);
        }
    }

    .g-inverted {
        #{$tag},
        .g-#{$tag} {
            color: $typo-color-text-inverted;
        }
    }
}

.g-faded {
    color: $typo-color-text-on-grey-faded;
}

a {
    transition: $input-transition-property $input-transition-duration;
    border-radius: rem(4);
    color: $color-blue-300;
    font-weight: $typo-x-font-weight-semi-bold;

    &[tabindex] {
        // for links that might be lacking a proper [href] :-/
        &:not([tabindex='-1']) {
            cursor: pointer;
        }
    }

    &:hover {
        text-decoration: underline;
    }

    &:focus {
        outline: none !important; // stylelint-disable-line declaration-no-important
    }

    @include element-focus;

    &:active:where(:not(.g-button)) {
        color: $color-blue-400;
    }

    &:not([href])[aria-disabled='true'] {
        color: rgba($color-gray-300, 0.7);
    }
}

strong,
b {
    font-weight: $typo-p-font-weight-semi-bold;
}

p,
li {
    font-weight: $typo-p-font-weight;

    strong,
    b {
        font-weight: $typo-p-font-weight-semi-bold;
    }
}

label {
    font-weight: $typo-x-font-weight-semi-bold;

    strong,
    b {
        font-weight: $typo-p-font-weight-semi-bold;
    }
}

code {
    padding: rem(3) rem(6);
    border-radius: rem(4);
    background: $color-gray-100;
    color: $color-ui-red;

    pre > * {
        padding: 0;
    }
}

hr {
    margin: (rem(16) - $spacing-vertical-minimal) 0;
    border-top-color: $typo-color-separator-dark;
}

.g-tabular {
    @include typo-tabular-numbers;
}

// --- fancy lists -------------------------------------------------------------

li {
    @include typo-offset(p, $reset: true);

    list-style: none;

    &:first-child {
        padding-top: 0;
    }

    &:last-child {
        padding-bottom: 0;
    }
}

ul,
ol {
    padding-left: $typo-list-indent;
    font-weight: $typo-p-font-weight;

    ul,
    ol {
        padding-bottom: 0;
    }

    &:not(.g-ul-plain) {
        li {
            position: relative;

            &::before {
                @include background-svg(svg-ico-dot($typo-color-bullet));

                content: '';
                position: absolute;
                margin: em(4) 0 0 #{-$typo-list-indent};
            }
        }
    }

    &.g-ul-plain {
        padding-left: 0;
    }

    &.g-ul-small {
        @include typo(p-minor);
        @include typo-offset(small);
    }

    &.g-ul-compact {
        li {
            padding-top: 0;
            padding-bottom: 0;
        }
    }

    &.g-ul-checkmark {
        li {
            &::before {
                background-image: svg-ico-checkmark-circle($typo-color-bullet);
            }
        }
    }
}

// --- on product color --------------------------------------------------------

.g-inverted {
    --color: #{$typo-color-text-inverted};

    color: var(--color);
}

:where(.g-inverted) {
    a {
        color: inherit;
    }

    hr {
        border-top-color: rgba($color-black, 0.1);
    }

    .g-faded {
        color: $typo-color-text-inverted-faded;
    }
}
