// --- product summary ---------------------------------------------------------

$product-info-icon-size: rem(48);
$product-info-gap-size: rem(10);

.g-product-info {
    display: flex;
    gap: $product-info-gap-size;

    .g-product-info-icon, // DEPRECATED
    .g-avatar {
        flex-shrink: 0;
        order: -1;
        color: $typo-color-text;
    }

    > .g-button:not(.g-button-avatar) {
        z-index: z-index-level(base, 1);
        align-self: flex-start;
        order: -2;
        margin-right: -$product-info-gap-size;
    }
}

.g-product-info-data {
    display: grid;
    flex-grow: 1;
    flex-shrink: 1;
    width: 100%;
    min-width: 0;
    margin: rem(-4) 0 rem(-2);

    .g-major {
        @include ellipsis;

        color: var(--g-color-text-primary);
        font-weight: $typo-x-font-weight-semi-bold;

        &.g-no-ellipsis {
            margin-top: rem(2);
            margin-bottom: rem(2);
            line-height: 1.25;
        }
    }

    .g-minor {
        @include ellipsis;
        @include typo(p-minor);

        .g-amount {
            @extend %amount-no-superscript;
            @include typo(p-minor);

            font-weight: inherit;
        }
    }

    .g-micro {
        @include ellipsis;
        @include typo(p-micro);
    }

    strong.g-minor {
        order: -1;
        font-weight: inherit;

        &:not(:last-child) {
            margin-bottom: rem(-2);
        }
    }

    .g-amount + .g-amount {
        margin-top: em(-4);
    }

    .g-amount {
        @include typo(p-major);

        display: block;
    }
}

.g-product-info-striped {
    padding-top: 2 * $colored-stripe-size;

    &.g-product-info-horizontal {
        padding-top: 0;
        padding-left: $colored-stripe-size;
    }
}

.g-product-info-horizontal {
    align-items: center;

    .g-product-info-stripe {
        right: auto;
        bottom: 0;
        left: 0;
        width: $colored-stripe-size;
        height: auto;
    }

    .g-product-info-data {
        gap: 0 $product-info-gap-size;
        align-items: center;
        margin-top: 0;
        margin-bottom: 0;
    }

    .g-product-info-badge {
        margin-top: rem(-2); // fake .g-micro line height in -horizontal
        margin-bottom: rem(-4); // ^ .g-micro line height in -horizontal
    }

    &.g-product-info-totaled {
        @include media-breakpoint-up(sm) {
            .g-product-info-data {
                grid-template-columns: 1fr max-content;
                grid-template-rows: repeat(6, max-content);
            }

            .g-product-info-amount {
                grid-column: 2 / 3;
                grid-row: 1 / 99; // "unnecessary" high = .g-product-info-data must not have more than 99 lines
                text-align: right;
            }
        }
    }

    @include no-standout-amount-below-large;
}

.g-product-info-right {
    .g-avatar {
        order: 0;
    }

    .g-product-info-amount {
        .g-amount {
            margin-top: rem(4);
        }
    }
}
