// taken from the bootstrap source to get rid of deprecation warnings ...

@mixin make-container($gutter: $grid-gutter-width) {
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    padding-right: $gutter * 0.5;
    padding-left: $gutter * 0.5;
}

@mixin make-row($gutter: $grid-gutter-width) {
    display: flex;
    flex-wrap: wrap;
    margin-right: -$gutter * 0.5;
    margin-left: -$gutter * 0.5;
}

@mixin make-container-max-width(
    $container-max-width,
    $breakpoint,
    $breakpoints: $grid-breakpoints
) {
    @include media-breakpoint-up($breakpoint, $breakpoints) {
        max-width: $container-max-width;
    }
}

@mixin make-container-max-widths(
    $max-widths: $container-max-widths,
    $breakpoints: $grid-breakpoints
) {
    @each $breakpoint, $container-max-width in $max-widths {
        @include make-container-max-width($container-max-width, $breakpoint, $breakpoints);
    }
}

@mixin make-col-ready($gutter: $grid-gutter-width) {
    position: relative;
    width: 100%;
    padding-right: $gutter * 0.5;
    padding-left: $gutter * 0.5;
}

@mixin make-col($size, $columns: $grid-columns) {
    flex: 0 0 percentage(sass-div($size, $columns));
    max-width: percentage(sass-div($size, $columns));
}
