// for things we need to change of bootstrap

& {
    box-sizing: border-box;
    overflow-wrap: anywhere;
    word-break: break-word;
}

.container,
.container-fluid {
    &::before,
    &::after {
        // fix for bootstrap 3 container styles
        content: none;
    }
}

// --- basic site setup --------------------------------------------------------

@at-root {
    &,
    html {
        @include fontstack;

        font-size: 16px;
        line-height: 1.5;
    }

    html,
    body {
        margin: 0;
        padding: 0;
    }

    &,
    body {
        color: $typo-color-text; // main font color
    }

    body {
        min-height: 100%;
        overflow-x: hidden;
        overflow-y: scroll;
        background-color: $color-gray-100;
        font-family: inherit;
        font-size: inherit;
        -moz-osx-font-smoothing: inherit;
        -webkit-font-smoothing: inherit;
        letter-spacing: normal;
        line-height: inherit;
        text-align: left;
        text-rendering: optimizeLegibility;
    }

    .modal-open {
        overflow: hidden;
        pointer-events: none;
    }

    .container,
    .container-fluid {
        & & {
            padding-right: 0;
            padding-left: 0;
        }
    }

    .row {
        & > & {
            width: 100%;
            margin-right: 0;
            margin-left: 0;
        }
    }

    [class^='col'] {
        & > & {
            padding-right: 0;
            padding-left: 0;
        }
    }
}

// --- advanced site setup -----------------------------------------------------

@mixin bs5-grid {
    .container,
    .container-xs,
    .container-sm,
    .container-md,
    .container-lg,
    .container-xl,
    .container-xxl {
        @include media-breakpoint-up(xxl) {
            @content;
        }
    }
}

@mixin bs4-grid-extended {
    .container,
    .container-xs,
    .container-sm,
    .container-md,
    .container-lg,
    .container-xl,
    .container-xxl {
        @include media-breakpoint-up(xl, $grid-breakpoints-extended) {
            max-width: map-get($map: $container-max-widths-extended, $key: xl);
        }
        @include media-breakpoint-up(xxl, $grid-breakpoints-extended) {
            max-width: map-get($map: $container-max-widths-extended, $key: xl);
        }
    }
}

@mixin bs5-grid-extended {
    .container,
    .container-xs,
    .container-sm,
    .container-md,
    .container-lg,
    .container-xl,
    .container-xxl {
        @include media-breakpoint-up(xxl, $grid-breakpoints-extended) {
            max-width: map-get($map: $container-max-widths-extended, $key: xxl);
        }
    }
}

@at-root {
    .g-bootstrap {
        @include bs5-grid {
            max-width: map-get($map: $container-max-widths, $key: xl);
        }

        &.g-bs5-grid {
            @include bs5-grid {
                max-width: map-get($map: $container-max-widths, $key: xxl);
            }
        }

        &.g-extended-grid {
            @include bs4-grid-extended;
        }

        &.g-bs5-grid.g-extended-grid {
            @include bs5-grid-extended;
        }

        &.g-fluid-grid {
            @include increase-specificity(1) {
                max-width: none;
            }
        }
    }
}
