@mixin functioncard-p-minor {
    @include typo(small);
}

.g-card-function,
.g-card-function-footer {
    --card-padding-vertical: #{$functioncard-spacing};

    @include media-breakpoint-up(sm) {
        --card-padding-horizontal: #{$functioncard-padding};
    }
}

.g-card-function {
    // --- content ---

    .g-h-major {
        @include increase-specificity(1) {
            // mimic FunctionCard heading, which is an unaltered Card heading
            @include typo-offset(h3);
        }

        @include typo(h3);

        font-weight: $typo-h-font-weight;
    }

    .g-h-minor {
        @include typo(small);

        font-weight: $typo-h-font-weight;
    }

    .g-p-regular {
        // DEPRECATED will be removed in v3
        @include typo(p);
    }

    .g-ul-minor,
    .g-p-minor {
        // DEPRECATED use .g-small instead
        @include functioncard-p-minor;
    }

    .g-group {
        margin: 0;
        padding-top: rem(10);
        padding-bottom: rem(10);
    }

    .g-group-button {
        width: 100%;
        text-align: center;

        @include media-breakpoint-up(sm) {
            @include flex-first;
        }
    }

    // --- layout/elements ---

    display: flex !important; // stylelint-disable-line declaration-no-important
    flex-direction: column;

    .card-body, // DEPRECATED
    .g-card-body {
        display: flex;
        flex-direction: column;
        flex-grow: 0;
    }

    .g-card-function-indicator {
        @include flex-first;

        align-items: center;
        margin-bottom: -$functioncard-spacing;
        color: $color-gray-300;
        text-align: center;

        &.g-indicator-custom {
            @include media-breakpoint-down(sm) {
                padding-top: $functioncard-padding;
                padding-bottom: $functioncard-padding;
            }

            .g-card-function-indicator-content {
                max-width: rem(200);

                // DEPRECATED:
                img {
                    max-width: 100%;
                }
            }
        }

        &.g-indicator-enabled {
            .g-state {
                // TODO: re-enable for whole container once legacy function cards are gone
                color: $color-ui-green;
            }
        }
    }

    .g-card-function-icon {
        --size: #{rem(48)};

        color: inherit;
    }

    .g-state {
        margin-top: rem(10);
        margin-right: rem(-8);
        margin-left: rem(-8);
        font-size: rem(12);
        font-weight: $typo-x-font-weight-semi-bold;
        line-height: rem(12);
        hyphens: auto;
    }

    .g-card-function-postscript {
        margin-top: -$functioncard-spacing;
        padding-top: 0;

        p {
            @include functioncard-p-minor;
        }
    }

    @include media-breakpoint-only(xs) {
        .g-card-function-postscript {
            .btn,
            .g-button {
                @include btn-group-neighbouring;

                width: 100%;
            }
        }
    }
}

.g-inverted {
    .g-card-function {
        .g-card-function-indicator {
            color: $typo-color-text-inverted-faded;

            &.g-indicator-enabled {
                .g-state {
                    color: $typo-color-text-inverted;
                }
            }
        }
    }
}

// --- more than one per row on MD+ -----------

.g-card-group-horizontal .g-card-function:where(:not([class*="g-card-function-variant-"])), // DEPRECATED
.g-card-function.g-card-function-variant-vertical {
    &:not(.g-card-function-plugin) {
        .g-card-function-icon {
            margin-top: rem(10);
        }
    }

    @include media-breakpoint-up(sm) {
        .card-body, // DEPRECATED
        .g-card-body {
            text-align: center;
        }
    }

    @include media-breakpoint-up(md) {
        .card-body, // DEPRECATED
        .g-card-body {
            flex-grow: 0;
        }

        .g-card-function-postscript {
            .btn,
            .g-button {
                max-width: calc(100% - #{$button-spacing-horizontal});
            }
        }
    }
}

// --- 3-column layout: indicator | text | buttons ---

.g-card-group-vertical .g-card-function:where(:not([class*="g-card-function-variant-"])), // DEPRECATED
.g-card-function.g-card-function-variant-horizontal {
    @include media-breakpoint-up(md) {
        flex-direction: row;

        .card-body, // DEPRECATED
        .g-card-body {
            justify-content: center;
        }

        .g-card-function-indicator {
            flex: 0 0 $functioncard-horizontal-indicator-width-regular;
            max-width: $functioncard-horizontal-indicator-width-regular;
            margin-bottom: 0;
            padding-right: 0;

            &.g-indicator-custom {
                flex: 0 0 $functioncard-horizontal-indicator-width-large;
                max-width: $functioncard-horizontal-indicator-width-large;
            }
        }

        .g-card-function-icon {
            @include g-image-64;

            margin-top: 0;
        }

        .g-card-function-body {
            flex: 1 0 0;

            --card-padding-vertical: #{$functioncard-spacing-prominent};

            text-align: left;
        }

        .g-card-function-postscript {
            flex: 0 0 $functioncard-horizontal-button-width;
            max-width: $functioncard-horizontal-button-width;
            margin-top: 0;
            padding-top: $functioncard-spacing;
            padding-left: 0;
            text-align: center;

            .g-group-button {
                // adjust spacing in case the postscript area has the most content of all 3 columns
                $functioncard-spacing-buttons: $functioncard-spacing + $functioncard-spacing * 0.5;

                padding: calc(var(--card-padding-horizontal) - #{$functioncard-spacing-buttons}) 0;
            }

            .btn,
            .g-button {
                width: 100%;
                margin-right: 0;
                margin-left: 0;
                padding-right: rem(5);
                padding-left: rem(5);
                @include btn-group-neighbouring;
            }
        }
    }
}

// --- shared footer for vertical function cards ---

.g-card-function-footer-shared {
    @include media-breakpoint-up(md) {
        @include increase-specificity(2) {
            margin-top: -$functioncard-spacing;
        }
    }
}

.g-card-function-footer {
    @include media-breakpoint-down(sm) {
        border-top-width: rem(1);
    }

    @include media-breakpoint-up(xl) {
        --card-padding-horizontal: 10%;
    }

    .card-body, // DEPRECATED
    .g-card-body {
        @include media-breakpoint-up(md) {
            padding-top: 0;
        }

        @include media-breakpoint-up(sm) {
            text-align: center;
        }
    }

    p {
        @include functioncard-p-minor;
    }
}
