$animation-duration-quick: 100ms;
$animation-easing-default: ease-out;

@keyframes appear {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.tooltip {
    z-index: z-index-level(tooltip);
    animation: appear $animation-easing-default $animation-duration-quick;
    opacity: 1;
    font-family: inherit;

    .tooltip-inner {
        background-color: var(--g-color-text-primary);
        text-align: left;

        &,
        p,
        li {
            font-size: rem(14);
            font-weight: $typo-x-font-weight-regular;
            letter-spacing: normal;
            line-height: rem(18);
        }
    }

    &.tooltip-top,
    &.bs-tether-element-attached-bottom {
        .tooltip-inner {
            &::before {
                border-top-color: var(--g-color-text-primary);
            }
        }
    }

    &.tooltip-bottom,
    &.bs-tether-element-attached-top {
        .tooltip-inner {
            &::before {
                border-bottom-color: var(--g-color-text-primary);
            }
        }
    }

    &.tooltip-left,
    &.bs-tether-element-attached-right {
        .tooltip-inner {
            &::before {
                border-left-color: var(--g-color-text-primary);
            }
        }
    }

    &.tooltip-right,
    &.bs-tether-element-attached-left {
        .tooltip-inner {
            &::before {
                border-right-color: var(--g-color-text-primary);
            }
        }
    }
}
