$input-color: $typo-color-text;
$input-border-width: rem(1);
$input-bordercolor: $color-gray-300;
$input-bordercolor-hover: rgba($color-blue-300, 0.6);
$input-transition-property: box-shadow;
$input-transition-duration: 250ms;
$input-fake-shadow: linear-gradient(180deg, rgba($color-black, 0.07), rgba($color-white, 0) rem(3));
$input-fake-shadow-rounded: radial-gradient(
    circle at 50% 100%,
    rgba($color-white, 0),
    rgba($color-white, 0) 75%,
    rgba($color-black, 0.4) 125%
);

$input-form-spacing: rem(16);
$input-control-size: rem(40);
$input-control-shift: rem(6);
$input-label-content-responsive-gap: rem(15);

$input-label-color: $typo-color-text;

$input-disabled-color: rgba($color-gray-300, 0.8);
$input-disabled-border: $input-disabled-color;
$input-disabled-background: mix($color-white, $color-gray-100, 50%);

$input-button-color: $color-blue-300;
$input-button-background: $color-white;

$input-selected-color: $color-blue-400;

$input-control-padding-diff: $input-control-size - map-get($typo-line-height, p);
$input-control-padding: $input-control-padding-diff * 0.5 - $input-border-width;
