%amount-superscript {
    sup {
        display: inline-block;
        position: static;
        top: auto;
        left: auto;
        transform: translate(em(sass-div(16, -12)), em(-7));
        font-size: em(sass-div(16 * 2, 3));
        line-height: 0;
        vertical-align: baseline;
    }
}

%amount-no-superscript {
    sup {
        display: inline;
        position: static;
        top: auto;
        left: auto;
        font-size: inherit;
        font-weight: inherit;
        vertical-align: baseline;
    }
}

@mixin no-standout-amount-below-large {
    @include media-breakpoint-down(md) {
        .g-amount {
            font-size: inherit;

            sup {
                margin-left: 0;
                transform: none;
                font-size: inherit;
            }
        }
    }
}

.g-amount {
    color: inherit;
    font-weight: $typo-x-font-weight-semi-bold;

    &,
    &.g-amount-no-superscript {
        @extend %amount-no-superscript;
    }

    &.g-amount-superscript {
        @extend %amount-superscript;
    }

    &.g-amount-camouflage {
        font-weight: inherit;
    }

    .g-amount-data {
        white-space: nowrap;
    }

    @include increase-specificity(1) {
        &.g-amount-default {
            color: inherit;
        }

        &.g-amount-neutral {
            color: var(--g-color-text-primary);
        }

        &.g-amount-positive {
            color: var(--g-color-success);
        }

        &.g-amount-negative {
            color: var(--g-color-error);
        }

        &.g-amount-faded {
            color: var(--g-color-text-secondary);
        }

        &.g-amount-active {
            color: var(--g-color-info);
        }
    }
}

.g-amount-fallback {
    color: var(--g-color-text-secondary);
    font-weight: inherit;
    gap: rem(4);

    @include increase-specificity(2) {
        display: inline-flex;
        font-size: rem(14);
        line-height: rem(20);
    }

    @include media-breakpoint-only(xs) {
        flex-direction: row-reverse;
        justify-content: flex-end;
    }
}

.g-card {
    .g-amount {
        &.g-amount-faded {
            color: var(--g-color-text-secondary);
        }
    }
}

%heading {
    .g-amount {
        font-weight: inherit;
    }
}

h1,
h2,
h3,
h4,
.g-h1,
.g-h2,
.g-h3,
.g-h4,
.g-huge,
.g-card-overview-grid-amounts,
.g-card-overview-info-amount,
.g-product-info {
    .g-amount {
        @extend %amount-superscript;
    }
}
