// --- tweak the bootstrap cards a bit -----------------------------------------

.g-card {
    display: inline-flex;
    width: 100%;
    max-width: 100%;
    margin-top: $spacing-vertical-half;
    margin-bottom: $spacing-vertical-half;
    overflow: hidden;
    border-width: 0;
    border-style: solid !important; // stylelint-disable-line declaration-no-important
    border-radius: $card-border-radius;
    border-color: var(
        --g-color-border-secondary
    ) !important; // stylelint-disable-line declaration-no-important

    background-color: var(--g-color-surface-primary);
    color: var(--g-color-text-primary);

    @include container-shadow;

    --card-padding-vertical: #{$card-padding-vertical-real};
    --card-padding-horizontal: #{$card-padding-horizontal};

    h2,
    h3,
    h4,
    h5,
    h6 {
        @include typo-offset(h3);
        @include typo(h3);

        font-weight: $typo-h-font-weight;
    }

    hr {
        border-top-color: var(--g-color-border-secondary);
    }

    .card-body, // DEPRECATED
    .g-card-body {
        flex: 1 1 auto;
        padding: var(--card-padding-vertical) var(--card-padding-horizontal);
    }

    @include increase-specificity(4) {
        &.g-card-no-padding {
            --card-padding-vertical: 0;
            --card-padding-horizontal: 0;
        }
    }
}

// --- specialized cards: headers ----------------------------------------------

%card-header {
    background-color: var(--g-color-surface-tertiary);
    color: var(--g-color-text-primary);
    text-align: center;

    p {
        margin: 0;
        padding: 0;
    }
}

// stylelint-disable-next-line no-duplicate-selectors
.g-card {
    &.g-card-header-small {
        @extend %card-header;

        > .card-body, // DEPRECATED
        > .g-card-body {
            padding-top: rem(12) - $typo-p-offset-top;
            padding-bottom: rem(12) - $typo-p-offset-bottom;
        }
    }

    &.g-card-header-large {
        @extend %card-header;

        --card-padding-vertical: 0;

        min-height: rem(56);
        padding: 0;

        .g-card-header-left {
            justify-content: flex-start !important; // stylelint-disable-line declaration-no-important

            @include media-breakpoint-down(sm) {
                a,
                .btn {
                    width: auto; // don't make buttons 100% width

                    // hide the button labels(if any) on small devices
                    > span:not(.sr-only) {
                        display: none;
                    }
                }
            }

            a {
                > span:not(.sr-only) {
                    margin-left: rem(4);
                }
            }
        }

        .g-card-header-right {
            justify-content: flex-end !important; // stylelint-disable-line declaration-no-important

            @include media-breakpoint-down(sm) {
                .btn {
                    width: auto; // don't make buttons 100% width

                    // hide the button labels(if any) on small devices
                    > span:not(.sr-only) {
                        display: none;
                    }
                }
            }
        }

        .g-card-header-icon {
            vertical-align: em(-4);
        }

        [class^='col-'] {
            @include flex-row-center-center;

            min-height: rem(56);
        }
    }

    &.g-card-header-info {
        @extend %card-header;

        display: flex;
        background: var(--g-color-surface-primary);
        text-align: center;

        --card-padding-vertical: #{rem(32)};

        @include media-breakpoint-down(sm) {
            --card-padding-vertical: #{rem(16)};
        }

        .g-amount {
            @include increase-specificity(1) {
                display: inline-block;
                margin-top: rem(20);
                font-size: rem(28);
                font-weight: $typo-x-font-weight-semi-bold;
                line-height: initial;
            }

            &:not(.g-amount-no-superscript) {
                @extend %amount-superscript;
            }
        }

        .g-product-info {
            flex-direction: column;
            gap: rem(8);
            width: 100%;

            .g-product-info-data {
                margin-top: 0;
                margin-bottom: 0;

                &,
                > * {
                    display: block;
                }
            }

            .g-major {
                @include typo(h3);
            }

            .g-avatar {
                width: rem(64);
                height: rem(64);
            }

            &.g-product-info-totaled {
                @include media-breakpoint-up(sm) {
                    .g-product-info-data {
                        grid-template-columns: unset;
                        grid-template-rows: unset;
                    }

                    .g-product-info-amount {
                        grid-column: unset;
                        grid-row: unset;
                        text-align: unset;
                    }
                }
            }
        }

        .g-group-button {
            .g-button {
                margin-bottom: 0;
            }
        }

        &:not(.g-card-header-left) {
            .g-product-info {
                .g-avatar {
                    align-self: center;
                }
            }
        }

        &.g-card-header-left {
            text-align: left;

            .g-product-info {
                flex-direction: row-reverse;
                align-items: flex-start;

                &:not(.g-contact-info) {
                    .g-minor {
                        color: inherit;
                    }
                }
            }
        }
    }
}

@media (forced-colors: active) {
    .g-card-group {
        background-color: var(--g-color-surface-primary);
        // color won't be applied, this is used to have a "backplate"
    }

    .g-card {
        background-color: transparent !important; // stylelint-disable-line declaration-no-important
    }
}

// --- our version of card groups ----------------------------------------------

.g-card-group {
    margin-top: $spacing-vertical-half;
    margin-bottom: $spacing-vertical-half;
    overflow: hidden;
    border-radius: $card-border-radius;

    @include container-shadow;

    // remove margins, shadow and round corners from all cards
    .g-card {
        display: flex;
        margin-top: 0;
        margin-bottom: 0;
        overflow: visible;
        outline: none;
        box-shadow: none;
    }

    > .g-card,
    .g-card:not(.g-card .g-card) {
        border-radius: 0;
    }
}

.g-card-group-scrollable {
    flex-wrap: nowrap;
    overflow: auto;

    .g-card {
        max-width: none;
    }
}

.g-card-group-horizontal {
    .g-card {
        + .g-card {
            border-left-width: rem(1);
        }
    }

    @include media-breakpoint-down(sm) {
        &:where(:not(.g-card-group-scrollable)) {
            .g-card {
                flex-basis: 100%;
                // sm and down we go vertical
                flex-direction: column;
                border-left-width: 0 !important; // stylelint-disable-line declaration-no-important

                + .g-card {
                    border-top-width: rem(1);
                }
            }
        }

        &:where(.g-card-group-scrollable) {
            display: flex;

            .g-card {
                // min-width: calc(100% - #{rem(16)} - var(--card-padding-horizontal));
                min-width: rem(255); // keep in sync with $g-card-overview-width

                @include media-breakpoint-only(sm) {
                    min-width: rem(
                        sass-div(690, 3) /* mimicks width of a Card among two others on tier MD*/
                    );
                }
            }
        }
    }
}

.g-card-group-vertical {
    flex-direction: column;

    .g-card {
        + .g-card {
            border-top-width: rem(1);
        }

        // remove separator lines if a card has a background color
        &[class*='g-bg-'],
        &.g-card-header-small,
        &.g-card-header-large {
            &,
            & + .g-card {
                border-top-width: 0;
            }
        }
    }
}

// stylelint-disable-next-line no-duplicate-selectors
.g-card-group {
    + .g-card-group {
        margin-top: $spacing-vertical; // overrule the usual 50-50 margins as groups can't be inline-blocks
    }

    &.g-card-group-nogap {
        margin-bottom: 0;

        & + .g-card-group {
            &,
            & .g-card {
                margin-top: 0;
            }

            @include media-breakpoint-down(sm) {
                // add a line when no-gaping
                .g-card {
                    border-top-width: rem(1);
                }
            }
        }

        &:not(.g-card-group-noline) {
            & + .g-card-group {
                .g-card {
                    @include media-breakpoint-up(md) {
                        border-top-width: rem(1);
                    }
                }
            }
        }
    }

    &.g-card-group-noline {
        .g-card {
            // disable card lines via .g-card-group-noline, but only on MD+
            @include media-breakpoint-up(md) {
                border-width: 0;
            }
        }
    }
}

.g-card-group-nogap {
    @include border-bottom-radius(0);

    + .g-card-group {
        @include border-top-radius(0);
    }
}

.g-card,
.g-modal {
    $card-border: rem(1) solid var(--g-color-border-secondary);

    .g-card,
    .g-card-group {
        box-shadow: none;
    }

    .g-card-group {
        border-top: $card-border;
        border-right: $card-border;
        border-left: $card-border;
        box-shadow: none;

        &:not(.g-card-group-nogap) {
            border-top: $card-border;
            border-bottom: $card-border;
        }

        &.g-card-group-nogap {
            + .g-card-group {
                border-top: 0 none;

                &:not(.g-card-group-nogap) {
                    border-top: 0 none;
                    border-bottom: $card-border;
                }
            }
        }

        &.g-card-group-horizontal {
            @include media-breakpoint-down(sm) {
                .g-card {
                    border-left: 0 none;

                    &:first-child {
                        border-top: 0 none;
                    }
                }
            }
        }
    }

    :not(.g-card-group) {
        > .g-card {
            border: $card-border;
        }
    }

    %card-header {
        width: auto;
        max-width: none;
        margin: rem(-1) rem(-1) 0;
    }

    .g-card-header-small,
    .g-card-header-large {
        background: var(--g-color-border-secondary);
        color: var(--g-color-text-primary);
        font-weight: $typo-x-font-weight-semi-bold;
    }
}

.g-inverted {
    &.g-card,
    .g-card {
        border-color: transparent !important; // stylelint-disable-line declaration-no-important
        background-clip: content-box;
        background-color: rgba(var(--rgb-black), 0.1);
        box-shadow: none;
        color: var(--g-color-text-inverted-primary);

        .g-faded {
            color: var(--g-color-text-inverted-secondary);
        }
    }

    .g-card-group,
    &.g-card-group {
        box-shadow: none;
    }
}

// --- .g-card-footer ----------------------------------------------------------

$card-footer-padding-vertical: rem(16);
$card-footer-column-gap: rem(24);

.g-card-footer {
    .card-body, // DEPRECATED
    .g-card-body {
        min-height: rem(56);

        --card-padding-vertical: #{$card-footer-padding-vertical};
        --card-padding-horizontal: #{$card-padding-horizontal};
    }

    .g-button-size-small {
        margin-top: rem(-3);
        margin-bottom: rem(-1);
    }
}

.g-card-footer-summary {
    display: flex;
    gap: 0 $card-footer-column-gap;
    padding-top: 0;
    padding-bottom: 0;

    // DEPRECATED
    .g-btn-link-secondary {
        margin: rem(-4) rem(-11) rem(-2) !important; // stylelint-disable-line declaration-no-important
    }

    .g-amount {
        display: block;
    }
}

@mixin card-footer-wrap($basis: true) {
    .g-card-footer-summary {
        flex-wrap: wrap;
    }

    .g-card-footer-total {
        @if $basis {
            flex-basis: 100%;
        } @else {
            flex-grow: 1;
        }
    }
}

@mixin card-footer-total-right {
    .g-card-footer-total {
        text-align: right;
    }
}

.g-card-footer-wrap {
    &-never {
        @include card-footer-total-right;
    }

    &-below-sm {
        @include media-breakpoint-down(xs) {
            @include card-footer-wrap;
        }
    }

    &-auto {
        @include card-footer-wrap($basis: false);
        @include card-footer-total-right;
    }

    &-always {
        @include card-footer-wrap;
    }
}

.g-card-footer-label {
    flex-grow: 1;
    text-align: left;
}

.g-card-footer-total {
    text-align: right;

    &:first-child {
        flex-basis: 100%;
    }

    @include media-breakpoint-down(xs) {
        text-align: left;

        // DEPRECATED
        .g-btn-link-secondary {
            margin-right: 0 !important; // stylelint-disable-line declaration-no-important
            margin-left: 0 !important; // stylelint-disable-line declaration-no-important
        }
    }
}

.g-card-footer-description {
    padding-top: 0;
    padding-bottom: 0;

    &:not(:only-child) {
        @include typo(p-minor);
    }

    &:only-child {
        text-align: center;
    }
}

// based on the checkout page

$confirm-footer-spacing: rem(30);

.g-card-confirm-footer {
    .card-body, // DEPRECATED
    .g-card-body {
        padding-top: $confirm-footer-spacing - $spacing-vertical-half;
        padding-bottom: $confirm-footer-spacing - $spacing-vertical-half;
        text-align: center;
    }
}
