$modal-h2-offset-top: rem(6);
$modal-h2-offset-bottom: rem(5);
$modal-p-offset-top: rem(6);
$modal-p-offset-bottom: rem(6);
$modal-padding: rem(20);
$modal-padding-inner: rem(10);
$modal-padding-outer: $modal-padding - $modal-padding-inner;
$modal-spacing-between: rem(20);

$modal-max-width: rem(848) + $modal-padding * 2;
$modal-max-width-small-sm: rem(510) + $modal-padding * 2;
$modal-max-width-small-md: rem(400);

$modal-padding-vertical: rem(30);
$modal-padding-horizontal-min: rem(20);

$modal-header-height: rem(50);
$modal-header-padding: $spacing-horizontal-medium;
$modal-header-padding-small: rem(40);
$modal-footer-height: rem(30 + 46 + 30);
$modal-footer-height-small: rem(4);

@mixin modal-footer-empty($height: rem(20)) {
    .modal-footer {
        // if neither footerClassName nor footer is provided, we show an empty stripe
        &:empty {
            visibility: hidden;
            height: $height;
            min-height: $height;
            padding: 0;
            border-top-width: 0;
        }
    }
}

@mixin modal-dialog-width($breakpoint, $max-width: $modal-max-width) {
    $container-width: map-get($container-max-widths, $breakpoint);
    $modal-width: min($container-width, $max-width);

    min-width: min($container-width - $modal-padding, $max-width);
    max-width: min($container-width - $modal-padding, $max-width);
}

.modal-backdrop {
    z-index: z-index-level(modal);
    background-color: var(--g-color-overlay-primary);

    &.show {
        opacity: 1; // we have alpha already in the color
    }
}

.g-modal {
    z-index: z-index-level(modal, 1);
    padding: $grid-gutter-width * 0.5;

    @include media-breakpoint-up(sm) {
        padding: $grid-gutter-width;
    }

    .modal-open & {
        overflow-y: auto;
        pointer-events: auto;
    }

    // our modal width depends on the resolution. we want it to always fit the main content.
    .modal-dialog {
        margin: auto;
        color: var(--g-color-text-primary);

        @include media-breakpoint-down(xs) {
            min-width: 100%;
            max-width: 100%;
            margin-right: 0;
            margin-left: 0;
        }

        @each $breakpoint in (sm, md, lg) {
            @include media-breakpoint-up($breakpoint) {
                @include modal-dialog-width($breakpoint);
            }
        }
    }

    .modal-content {
        @include container-shadow($shadow-modal);

        min-height: rem(260);
        overflow: hidden;
        border: 0 none;
        border-radius: $card-border-radius;
        outline-width: rem(7);

        // --- regular text ----------------------------------------------------

        %heading {
            @include typo(h2);

            color: var(--g-color-text-primary);
            font-weight: $typo-h-font-weight;

            &:first-of-type {
                @include typo-offset(h2);
            }
        }

        .modal-header {
            flex-direction: column;
            align-items: center;
            justify-content: center;
            min-height: $modal-header-height;
            padding: $modal-padding-outer $modal-header-padding;
            border-color: var(--g-color-border-secondary);

            @include media-breakpoint-down(sm) {
                padding-right: $modal-header-padding-small;
                padding-left: $modal-header-padding-small;

                .g-label {
                    @include sr-only;
                }
            }

            %heading-tags {
                @include typo(h2);
                @include typo-offset(h2, $reset: true);

                border-bottom: 0 none;
                text-align: center;
            }

            .g-modal-header-icon {
                --size: #{rem(48)};

                display: block;
                margin-right: auto;
                margin-left: auto;
                color: var(--g-color-fill-primary);

                &:first-of-type {
                    margin-top: $modal-padding-inner - rem(2); // compensate a bit for the usual svg offsets
                }
            }

            .g-modal-stepping {
                @include typo(p);

                color: var(--g-color-text-secondary); // TODO: this whole block is deprecated
                text-align: center;
            }

            .modal-help,
            .modal-close {
                position: absolute;
                cursor: pointer;

                &:not(.btn, .g-button) {
                    top: 0;
                    padding-top: ($modal-padding - rem(5));
                    font-size: rem(14);
                }

                &.btn,
                &.g-button {
                    top: ($modal-padding - rem(7));
                }
            }

            .modal-close {
                &:not(.btn, .g-button) {
                    right: 0;
                    padding-right: ($modal-padding - rem(2));
                    color: var(--g-color-fill-primary); // TODO: this whole block seems unnecessary
                }

                &.btn,
                &.g-button {
                    right: rem(10);
                }
            }

            .modal-help {
                &:not(.btn, .g-button) {
                    left: 0;
                    padding-left: ($modal-padding - rem(5));
                }

                &.btn,
                &.g-button {
                    left: rem(10);
                }
            }
        }

        .modal-body {
            padding: ($modal-padding-vertical - $modal-padding-inner) $modal-padding;
            border-color: var(--g-color-border-secondary);

            @include media-breakpoint-up(lg) {
                padding-right: $spacing-horizontal-medium;
                padding-left: $spacing-horizontal-medium;
            }

            &.g-modal-body-large {
                @include media-breakpoint-up(lg) {
                    padding-right: $spacing-horizontal-small;
                    padding-left: $spacing-horizontal-small;
                }
            }

            &.g-modal-body-full {
                padding-right: $modal-padding-horizontal-min;
                padding-left: $modal-padding-horizontal-min;
            }

            &.g-modal-body-form-top {
                @include media-breakpoint-up(md) {
                    padding-top: $modal-padding-vertical - $modal-padding-inner + rem(10);
                }
            }

            &.g-modal-body-form-bottom {
                padding-bottom: $modal-padding-vertical - $modal-padding-inner + rem(10);
            }

            .container {
                padding-right: 0;
                padding-left: 0;
            }

            & + .modal-body {
                // if two bodies follow each other, we reduce the gap by removing the padding of the latter
                margin-top: -($modal-padding-vertical - $modal-padding-inner);
                padding-top: 0;
            }
        }

        .modal-image {
            padding: 0;
            border: 0 none;
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain;

            img {
                width: 100%;
            }
        }

        .modal-footer {
            justify-content: center;
            min-height: $modal-footer-height;
            padding: $modal-padding-outer $modal-padding;
            border-color: var(--g-color-border-secondary);

            &.g-modal-footer-grey {
                padding-top: 0;
                padding-bottom: rem(18);
                border-color: transparent;
                background-image: linear-gradient(
                    var(--g-color-surface-secondary) rem(88),
                    transparent rem(88)
                ); // TODO: this creates a gap between the bottom of the modal and the coloured background
            }
        }

        // remove separator lines if a modal element has a background color
        .modal-header,
        .modal-body {
            &[class*='g-bg-'] {
                border-bottom-width: 0;

                + .modal-footer {
                    border-top-width: 0;
                }
            }
        }
    }
}

// --- regular modals (specifc rules that do not apply to other types)----------

.g-modal-regular {
    .modal-dialog {
        @include modal-footer-empty;
    }
}

// --- small modals a.k.a. dialogs ---------------------------------------------

.g-modal-small {
    .modal-dialog {
        @include media-breakpoint-up(sm) {
            @include modal-dialog-width(sm, $modal-max-width-small-sm);
        }

        @include media-breakpoint-up(md) {
            @include modal-dialog-width(md, $modal-max-width-small-md);
        }

        .modal-content {
            width: auto;
            min-height: initial;
        }

        .modal-header {
            padding-right: $modal-header-padding-small;
            padding-left: $modal-header-padding-small;
            border-width: 0;
            background-color: var(--g-color-surface-secondary);
            color: var(--g-color-text-primary); // TODO: this seems unnecessary

            .modal-title {
                color: inherit;
            }

            &[class*='g-bg-'] {
                // swap text color if the modal header has a non-default background color
                color: var(--g-color-text-inverted-primary); // TODO: this seems unnecessary
            }
        }

        .modal-body {
            padding-right: $modal-padding-horizontal-min;
            padding-left: $modal-padding-horizontal-min;
        }

        @include modal-footer-empty($modal-footer-height-small);
    }
}

// --- success -----------------------------------------------------------------

.g-modal-confirmation {
    .modal-dialog {
        .modal-content {
            %heading-tags {
                padding-bottom: rem(30) - $typo-p-offset-bottom - $spacing-vertical-half +
                    $typo-p-offset-top;
                text-align: center;
            }

            .modal-header {
                border: none;

                .modal-title {
                    margin-bottom: rem(-30);
                }

                .modal-title-hidden {
                    visibility: hidden;
                }
            }

            .modal-body {
                padding-bottom: rem(30) - map-get($typo-offset-bottom, h3);

                @include media-breakpoint-down(xs) {
                    p {
                        text-align: left;
                    }
                }

                + .modal-body {
                    margin-top: -(rem(30) - map-get($typo-offset-bottom, h3));
                }
            }

            .g-modal-confirmation-icon {
                --size: #{rem(128)};

                margin-top: rem(98);
                margin-bottom: rem(30 - 8 - 5);

                @include media-breakpoint-down(sm) {
                    margin-top: rem(60);
                }
            }

            .modal-footer {
                border: 0 none;
            }
        }
    }
}

// --- teaser -----------------------------------------------------------------

.g-modal-teaser {
    .modal-content {
        .modal-header {
            padding-block: rem(16);
            border-width: 0;
        }

        .modal-title {
            text-align: center;

            @include increase-specificity(1) {
                padding-top: rem(24);
            }
        }

        .modal-body {
            padding-top: 0;
        }

        .modal-image {
            display: flex;
            align-items: center;
            min-height: rem(160);
            max-height: rem(480);
        }

        .modal-footer {
            flex-direction: column;
        }
    }
}

// --- embedded in page, e.g. in docs ------------------------------------------

// stylelint-disable-next-line no-duplicate-selectors
.g-modal {
    &.g-modal-embedded {
        display: block;
        position: relative;
        z-index: z-index-level(base);
        margin: 0 auto;
        padding: 0;
        overflow: visible;

        + .g-modal {
            margin-top: $spacing-vertical;
        }

        .modal-dialog {
            flex-basis: 100%;
            min-width: 0;
            margin: auto;
        }

        .modal-content {
            min-height: 0;
            box-shadow: 0 0 rem(8) rgba(var(--rgb-black), 0.2);
        }
    }
}
