.g-spinner {
    --size: #{rem(32)};
    --stroke-width: #{rem(3)};
    --color: currentColor;

    width: var(--size) !important; // stylelint-disable-line declaration-no-important
    height: var(--size) !important; // stylelint-disable-line declaration-no-important
    animation: rotate 2s linear infinite;
    animation-delay: -1s;

    &-small {
        --size: #{rem(24)};
        --stroke-width: #{rem(4)};
    }

    &-large {
        --size: #{rem(48)};
        --stroke-width: #{rem(2)};
    }

    &-circle {
        animation: dash 2s ease-in-out infinite;
        animation-delay: -1s;
        stroke-width: var(--stroke-width);
        stroke-dasharray: 80, 200;
        stroke-dashoffset: 0;
        stroke: var(--color);
    }
}

@keyframes rotate {
    0% {
        transform-origin: 50% 50%;
    }

    100% {
        transform: rotate(360deg);
    }
}

@keyframes dash {
    0% {
        stroke-dasharray: 1, 200;
        stroke-dashoffset: 0;
    }

    50% {
        stroke-dasharray: 100, 200;
        stroke-dashoffset: -15;
    }

    100% {
        stroke-dasharray: 100, 200;
        stroke-dashoffset: -125;
    }
}
