// --- groups ------------------------------------------------------------------

// groups are wrappers that help introduce proper margins for non-text elements
// e.g buttons

.g-group {
    // it is assumed that inside the group the elements fill the vertical space fully
    padding-top: $spacing-vertical-half;
    padding-bottom: $spacing-vertical-half;
}

// --- bootstrap containers & rows ---------------------------------------------

// p + .row, ul + .row, ol + .row, # breaks
h1,
h2,
h3,
h4,
h5,
h6 {
    & + .row {
        padding-top: $spacing-vertical-half + rem(8); // 8px = half of 16px = spacing between 2 input fields
    }
}
