$track-color: $color-gray-100 !default;
$thumb-color: $color-gray-100 !default;

$thumb-radius: 100% !default;
$thumb-height: rem(28) !default;
$thumb-width: rem(28) !default;
$thumb-shadow-size: 0 !default;
$thumb-shadow-blur: 0 !default;
$thumb-shadow-color: transparent !default;
$thumb-border-width: rem(1) !default;
$thumb-border-color: $color-gray-300 !default;

$track-width: 100% !default;
$track-height: rem(6) !default;
$track-shadow-size: 0 !default;
$track-shadow-blur: 0 !default;
$track-shadow-color: transparent !default;
$track-border-width: rem(1) !default;
$track-border-color: $thumb-border-color !default;

$track-radius: rem(3) !default;
$contrast: 0% !default;

@mixin shadow($shadow-size, $shadow-blur, $shadow-color) {
    box-shadow: $shadow-size $shadow-size $shadow-blur $shadow-color,
        0 0 $shadow-size lighten($shadow-color, 5%);
}

@mixin track {
    width: $track-width;
    height: $track-height;
    transition: all 0.2s ease;
    cursor: pointer;
}

@mixin thumb {
    @include shadow($thumb-shadow-size, $thumb-shadow-blur, $thumb-shadow-color);

    width: $thumb-width;
    height: $thumb-height;
    transition: $input-transition-property $input-transition-duration;
    border: $thumb-border-width solid $thumb-border-color;
    border-radius: $thumb-radius;
    background: $thumb-color;
    cursor: pointer;
}

input {
    &[type='range'] {
        &::-moz-focus-outer {
            border: 0 !important; // stylelint-disable-line declaration-no-important
            outline: none !important; // stylelint-disable-line declaration-no-important
        }
    }
}

.g-slider {
    width: $track-width;
    margin: ($thumb-height * 0.5) 0;
    appearance: none;

    &::-webkit-slider-runnable-track {
        @include track;
        @include shadow($track-shadow-size, $track-shadow-blur, $track-shadow-color);

        border: $track-border-width solid $track-border-color;
        border-radius: $track-radius;
    }

    &::-webkit-slider-thumb {
        @include thumb;

        appearance: none;
        margin-top: (-$track-border-width * 2 + $track-height - $thumb-height) * 0.5;
    }

    &::-moz-range-track {
        @include track;
        @include shadow($track-shadow-size, $track-shadow-blur, $track-shadow-color);

        height: $track-height - rem(2);
        border: $track-border-width solid $track-border-color;
        border-radius: $track-radius;
        background: $track-color;
    }

    &::-moz-range-thumb {
        @include thumb;
    }

    &::-ms-track {
        @include track;

        border-width: $thumb-height * 0.5 0;
        border-color: transparent;
        background: transparent;
        color: transparent;
    }

    &::-ms-fill-lower {
        @include shadow($track-shadow-size, $track-shadow-blur, $track-shadow-color);

        border: $track-border-width solid $track-border-color;
        border-radius: $track-radius * 2;
        background: $track-color;
    }

    &::-ms-fill-upper {
        @include shadow($track-shadow-size, $track-shadow-blur, $track-shadow-color);

        border: $track-border-width solid $track-border-color;
        border-radius: $track-radius * 2;
        background: $track-color;
    }

    &::-ms-thumb {
        @include thumb;

        margin-top: 0;
    }

    &:focus {
        outline: none;

        &::-webkit-slider-thumb {
            @include element-focus-ring(true);
        }

        &::-moz-range-thumb {
            @include element-focus-ring(true);
        }

        &::-ms-thumb {
            @include element-focus-ring(true);
        }
    }
}

// --- groups and labels -------------------------------------------------------

.g-slider + .g-slider-legend {
    margin-top: rem(5 - 14);
    // we want 15px. the legend has 10, so the slider should have 5px. but it has 14 ...
}
// hack for Firefox
// stylelint-disable-next-line function-url-quotes
@-moz-document url-prefix() {
    .g-slider + .g-slider-legend {
        margin-top: rem(5 - 14 - 12);
        // firefox adds an additional 12px space around the slider - remove it too.
    }
}

.g-slider-legend {
    .g-slider-left {
        float: left;
    }

    .g-slider-middle {
        text-align: center;
    }

    .g-slider-right {
        float: right;
    }
}
