$icon-badge-border-size: rem(1);
$icon-badge-min-size: rem(24);

// --- avatar circular images -------------------------------------------------

.g-avatar {
    @include g-image-core;
    @include flex-row-center-center;

    --avatar-background: var(--color-white);
    --avatar-size: var(--size);

    display: inline-block;
    position: relative;
    color: var(--g-color-fill-primary);
    vertical-align: middle;

    // DEPRECATED
    .g-avatar-hover {
        position: absolute;
        z-index: z-index-level(base, 1);
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        transition: $input-transition-property $input-transition-duration, opacity 150ms ease-out;
        border-radius: inherit;
        opacity: 0;
        background-color: var(--g-color-overlay-primary);
        cursor: pointer;

        &:hover {
            transition-timing-function: ease-in;
            opacity: 1 !important; // stylelint-disable-line declaration-no-important
        }

        &::before,
        svg {
            position: absolute;
            top: 25%;
            left: 25%;
            width: 50%;
            height: 50%;
        }

        &::before {
            content: '';
            border-radius: inherit;
            background-color: var(--g-color-surface-primary);
        }

        svg {
            padding: 10%;
            overflow: visible;
        }
    }

    .g-avatar-content {
        display: block;
        z-index: z-index-level(base, 0);
        width: 100%;
        height: 100%;
        overflow: hidden;
        border-radius: 50%;
        background-color: var(--avatar-background);
        line-height: 0;

        &::after {
            @include cover-parent($z-index: z-index-level(base, 1));

            content: '';
            border: rem(1) solid transparent;
            border-radius: 50%;
            pointer-events: none;
        }

        &-border::after {
            border-color: var(--g-color-border-secondary);
        }

        &-dark {
            background-image: linear-gradient(rgba(0, 0, 0, 10%), rgba(0, 0, 0, 10%));
        }
    }

    .g-avatar-image {
        object-fit: cover;
        width: 100%;
        height: 100%;

        &-contain {
            object-fit: contain;
        }
    }

    .g-avatar-icon {
        $avatar-padding-factor: 0.25;

        --size: calc(var(--avatar-size) * #{2 * $avatar-padding-factor}); // see padding below

        display: block;
        width: 100%;
        height: 100%;
        padding: 100% * $avatar-padding-factor;
        background-repeat: no-repeat;
        background-position: 50%;
        background-size: cover;
    }

    .g-avatar-initials {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        font-weight: $typo-x-font-weight-semi-bold;
    }

    &.g-image-48 .g-avatar-initials {
        font-size: map-get($typo-font-size, p);
    }

    &.g-image-64 .g-avatar-initials {
        font-size: map-get($typo-font-size, h2);
    }

    &.g-image-96 .g-avatar-initials {
        font-size: map-get($typo-font-size, h-huge);
    }

    &.g-image-128 .g-avatar-initials {
        font-size: map-get($typo-font-size, h-huge) * 1.5;
    }

    .g-avatar-badge {
        @include flex-row-center-center;

        position: absolute;
        z-index: z-index-level(base, 2);
        right: -7.5%;
        bottom: -7.5%;
        width: 37.5%;
        min-width: $icon-badge-min-size;
        height: 37.5%;
        min-height: $icon-badge-min-size;
        padding: calc(9% - #{$icon-badge-border-size}) calc(8% - #{$icon-badge-border-size});
        border: $icon-badge-border-size solid $color-white;
        border-radius: 50%;
        background-clip: padding-box; // improve badge rendering on Firefox
        background-color: var(--color-lavender);
        color: var(--color-white);

        svg {
            width: 100%;
            height: 100%;
        }
    }

    /* stylelint-disable no-duplicate-selectors */
    &.g-image-48 {
        .g-avatar-badge {
            right: rem(-6);
            bottom: rem(-6);
            padding: rem(5) rem(4);
        }
    }
}

@mixin g-button-avatar-state-layers {
    .g-button-icon + .g-button-label::before,
    .g-button-icon,
    .g-avatar-content::before {
        @content;
    }
}

.g-button-avatar {
    position: relative;
    border-radius: 50%;

    @include increase-specificity(1) {
        width: auto;
    }

    .g-avatar-content::before {
        @include cover-parent;

        content: '';
        border-radius: 50%;
        background-color: var(--g-color-overlay-primary);
    }

    .g-button-icon {
        @include cover-parent($offset: 25%, $z-index: z-index-level(base, 3));

        width: 50%;
        height: 50%;
        padding: 10%;
        overflow: visible;
        color: var(--g-color-fill-primary);

        + .g-button-label {
            &::before {
                @include cover-parent($offset: 25%, $z-index: z-index-level(base, 2));

                content: '';
                border-radius: 50%;
                background-color: var(--g-color-surface-primary);
            }

            @include increase-specificity(1) {
                margin-left: 0;
            }
        }
    }

    @include g-button-avatar-state-layers {
        transition: $input-transition-property $input-transition-duration, opacity 150ms ease-out;
        opacity: 0;
    }

    &:not(:disabled) {
        &:focus,
        &:hover {
            @include g-button-avatar-state-layers {
                transition-timing-function: ease-in;
                opacity: 1;
            }
        }
    }

    &:disabled {
        cursor: not-allowed;

        .g-button-icon {
            color: var(--g-color-fill-disabled);
        }

        .g-avatar-image {
            opacity: 0.5;
        }

        .g-avatar-badge {
            // same as .g-pill in _button.scss
            filter: grayscale(1);
            opacity: 0.1;
        }
    }
}
