@mixin color-helper-classes(
    $name,
    $color,
    $color-complement: $color-complement-dark,
    $rgb-overlay: $rgb-overlay-dark
) {
    .g-fg-#{$name} {
        --color-fg: #{$color};
    }

    .g-bg-#{$name} {
        --color-bg: #{$color};
        --color-fg: #{$color-complement};
        --rgb-overlay: #{$rgb-overlay};
    }
}

// add a $semiTransparentColor on top of a $baseColor
// (or omit $baseColor to make background shine through)
@mixin color-layer($semiTransparentColor, $baseColor: null) {
    @if $baseColor {
        background-color: $baseColor;
    }

    background-image: linear-gradient($semiTransparentColor, $semiTransparentColor);
}
