@mixin flex-row-center-center {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

@mixin flex-row-stretch-center {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

@mixin flex-row-stretch-top {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
}

@mixin flex-column-left-center {
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: center;
}

@mixin flex-column-right-center {
    display: flex;
    flex-direction: column;
    align-items: right;
    justify-content: center;
}

@mixin flex-column-center-center {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

@mixin flex-column-center-start {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
}

@mixin flex-column-stretch-center {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

@mixin flex-autoresize {
    flex-grow: 1;
    flex-shrink: 1;
    min-width: 0; // needed to prevent ellipsis to break the flex sizing, see https://css-tricks.com/flexbox-truncated-text/
}

@mixin flex-first {
    order: -1;
}

@mixin flex-last {
    order: 1;
}
