.g-focus-page {
    $focus-page-padding: rem(32);
    $focus-page-padding-small: rem(16);
    $focus-page-card-padding-vertical: $focus-page-padding - $card-padding-vertical-real;
    $focus-page-card-padding-horizontal: $focus-page-padding - $card-padding-horizontal;

    padding-right: 0 !important; // stylelint-disable-line declaration-no-important
    padding-left: 0 !important; // stylelint-disable-line declaration-no-important

    @include media-breakpoint-down(xs) {
        padding: 0 !important; // stylelint-disable-line declaration-no-important
    }

    %col {
        @include make-col-ready;
        @include make-col(12);

        margin: 0 auto;
    }

    .g-focus-page-stepper {
        @extend %col;

        padding: rem(16) $focus-page-padding-small $spacing-vertical-half;

        @include media-breakpoint-up(sm) {
            padding: rem(24) $focus-page-padding $spacing-vertical-half;
        }

        &:empty {
            display: none;
        }

        + .g-focus-page-content {
            padding-top: 0;
        }
    }

    .g-focus-page-buttons {
        @include media-breakpoint-down(xs) {
            display: flex;
            position: fixed;
            z-index: z-index-level(ontop);
            bottom: rem(0);
            justify-content: space-between;
            width: 100%;
            padding: rem(8);
            background: var(--g-color-surface-primary);
            box-shadow: 0 -2px 8px rgba(var(--rgb-black), 10%);

            .g-button {
                width: auto;

                &:nth-of-type(2) .g-button-label {
                    order: -1;
                    margin-right: rem(8);
                    margin-left: 0;
                }
            }

            + .g-focus-page-content {
                padding-bottom: rem(56);
            }
        }

        @include media-breakpoint-up(sm) {
            display: flex;
            position: sticky;
            top: rem(355);
            justify-content: space-between;
            height: 0;
            margin: 0 auto;

            .g-button {
                width: rem(40);
                border-radius: 50%;

                &:nth-of-type(1) {
                    transform: translateX(-100%);
                }

                &:nth-of-type(2) {
                    transform: translateX(100%);
                }
            }
        }

        + .g-focus-page-content {
            padding-top: 0;
        }
    }

    .g-focus-page-content {
        @extend %col;

        padding: ($focus-page-padding-small - $spacing-vertical-half) 0;

        @include media-breakpoint-up(sm) {
            padding: ($focus-page-padding - $spacing-vertical-half) ($grid-gutter-width * 0.5);

            .g-card {
                &[class*='g-card-header-'] {
                    --card-padding-horizontal: #{$focus-page-padding};
                }

                &.g-card-no-padding {
                    --card-content-padding-horizontal: #{$focus-page-padding};
                }

                // TODO refactor the following selector by making it specific
                &:not([class*='g-card-']) {
                    --card-padding-vertical: #{$focus-page-padding};
                    --card-padding-horizontal: #{$focus-page-padding};

                    > .card-body, // DEPRECATED
                    > .g-card-body {
                        > .g-product-info {
                            margin-top: -$focus-page-card-padding-vertical;
                            margin-bottom: -$focus-page-card-padding-vertical;
                            margin-left: -$colored-stripe-size;
                        }
                    }

                    .g-card {
                        > .card-body, // DEPRECATED
                        > .g-card-body {
                            > .g-product-info {
                                margin: 0;
                            }
                        }

                        --card-padding-vertical: #{$card-padding-vertical-real};
                        --card-padding-horizontal: #{$card-padding-horizontal};
                    }
                }
            }

            .g-card-group-vertical {
                &.g-card-group-noline {
                    > .g-card {
                        --card-padding-vertical: #{$card-padding-vertical-real};

                        &:first-of-type {
                            padding-top: $focus-page-card-padding-vertical;
                        }

                        &:last-of-type {
                            padding-bottom: $focus-page-card-padding-vertical;
                        }

                        &.g-card-no-padding {
                            padding: 0;
                        }
                    }
                }
            }

            .g-card-function {
                --card-padding-horizontal: #{rem(32)};
            }
        }

        @include media-breakpoint-down(xs) {
            .g-card,
            .g-card-group {
                @include increase-specificity(1) {
                    padding: 0;
                }
            }
        }

        @include media-breakpoint-down(xs) {
            > .g-card,
            > .g-card-group {
                @include increase-specificity(2) {
                    border-radius: 0;
                }

                box-shadow: none;
            }

            > ul,
            > ol {
                margin-left: rem(24);
            }

            @include increase-specificity(1) {
                & > * {
                    padding-right: $focus-page-padding-small;
                    padding-left: $focus-page-padding-small;
                }
            }
        }
    }

    @include media-breakpoint-up(sm) {
        .g-focus-page-small {
            @include make-col(10);
        }

        .g-focus-page-medium {
            @include make-col(10);
        }

        .g-focus-page-large {
            @include make-col(12);
        }
    }

    @include media-breakpoint-up(md) {
        .g-focus-page-small {
            @include make-col(8);
        }
    }

    @include media-breakpoint-up(lg) {
        .g-focus-page-small {
            @include make-col(6);
        }

        .g-focus-page-medium {
            @include make-col(8);
        }

        .g-focus-page-large {
            @include make-col(10);
        }
    }
}
